.models{
	position: relative;
	&::after {
		position: absolute;
		content: '';
		left: 0;
		bottom: 0;
		width: 100%;
		height: 40%;
		background: linear-gradient(to top, #4377d0 0%, #4379d1 100%);
		z-index: -1;
	}
	&__discr {
		@media(max-width: 1023px) {
			margin-top: 60px;
		}
	}
	&__container {
		width: 100%;
		padding-bottom: 110px;
		@media(max-width: 1200px) {
			//padding: 0 15px 80px;
			padding: 0 15px 40px;
		}
		// @media(max-width: 767px) {
		// 	padding: 0 15px 40px;
		// }
		@media(max-width: 450px) {
		padding: 0 10px 20px;
		}
	}
}
.tab-panel {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	max-width: 1155px;
	margin: 0 auto;
	margin-top: 60px;
	padding: 55px 72px 40px;
	box-shadow: 0 4px 51px 8px rgba(0, 0, 0, 0.06);
	border-radius: 14px;
	background-image: linear-gradient(to top, #f8fafc 0%, #ffffff 100%);
	@media(max-width: 1200px) {
		padding: 45px 45px 25px;
	}
	@media(max-width: 1023px) {
		flex-direction: column;
		padding: 25px 25px 15px;
		margin-top: 40px;
	}
	// @media(max-width: 900px) {
	// 	padding: 25px 25px 15px;
	// }
	@media(max-width: 450px) {
		margin-top: 15px;
	}
	&__left-block {
		margin-right: 40px;
		@media(max-width: 1023px) {
			width: 100%;
			margin-right: 0;
		}
	}
	&__model-img {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 40%;
	}
	&__model-info {
		width: 60%;	
	}
	&__right-block {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		@media(max-width: 1023px) {
			margin-top: 10px;
			//margin-top: 30px;
		}
		@media(max-width: 450px) {
			margin-top: 20px;
		}
	}
}
.model-tab {
	@media(max-width: 1023px) {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		width: 485px;
		margin: 0 auto;
	}
	@media(max-width: 600px) {
		width: 355px;
	}
	@media(max-width: 450px) {
		width: 250px;
		margin-top: 20px;
		
	}
	&--bottom {
		margin-top: 35px;
		@media(max-width: 1023px) {
			margin-top: 15px;
			//margin-top: 30px;
		}
		@media(max-width: 450px) {
			margin-top: 5px;
		}
	}
	&__item {
		position: relative;
		width: 240px;
		padding: 14px 15px;
		margin-top: -3px;
		margin-left: -3px;
		font-family: @ff;
		font-size: 20px;
		font-weight: 700;
		color: #8b8b8b;
		text-align: center;
		text-transform: uppercase;
		letter-spacing: 0.75px;
		border-radius: 8px;
		overflow: hidden;
		cursor: pointer;
		background-color: rgba(201, 202, 203, 0.5);
		@media(max-width: 1023px) {
			padding: 12px 15px;
			font-size: 16px;
		}
		// @media(max-width: 900px) {
		// 	padding: 12px 15px;
		// 	font-size: 16px;
		// }
		@media(max-width: 600px) {
			width: 175px;
		}
		@media(max-width: 450px) {
			width: 124px;
			padding: 8px;
			font-size: 11px;
		}
		&:hover {
			background-color: #d4d4d5;
		}
	}
	&__item-active {
		color: #ffffff;
		background-color: #3d82d8;
		z-index: 1;
		&:hover {
			background-color: #3072c3;
		}
	}
}
.info-panel {
	width: 100%;
	max-width: 325px;
	padding: 10px;
	font-family: @ff;
	font-weight: 400;
	color: #989898;
	@media(max-width: 1023px) {
		width: 50%;
		margin-left: 0;
	}
	// @media(max-width: 900px) {
	// 	width: 65%;
	// 	margin-left: 10px;
	// }
	@media(max-width: 450px) {
		width: 50%;
		margin-left: 0;
	}
	&__text {
		position: relative;
		padding-bottom: 25px;
		border-radius: 1px;
		border-bottom: 2px solid #c5c5c5;
		@media(max-width: 450px) {
			padding-bottom: 7px;
		}
	}
	&__product-type {
		font-size: 20px;
		color: #858585;
		letter-spacing: 0.22px;
		text-transform: lowercase;
		@media(max-width: 1023px) {
			font-size: 16px;
		}
		// @media(max-width: 900px) {
		// 	font-size: 16px;
		// }
		@media(max-width: 450px) {
			font-size: 11px;
		}
	}
	&__title {
		margin-top: 10px;
		font-size: 32px;
		font-weight: 700;
		text-transform: uppercase;
		color: #5a5a5a;
		letter-spacing: 0.36px;
		@media(max-width: 1023px) {
			margin-top: 5px;
			font-size: 26px;
		}
		// @media(max-width: 900px) {
		// 	margin-top: 5px;
		// 	font-size: 26px;
		// }
		@media(max-width: 450px) {
			font-size: 17px;
		}
	}
	&__promo-text {
		margin-top: 10px;
		font-size: 17px;
		letter-spacing: 0.24px;
		@media(max-width: 1023px) {
			margin-top: 5px;
			font-size: 15px;
		}
		// @media(max-width: 900px) {
		// 	margin-top: 5px;
		// 	font-size: 15px;
		// }
		@media(max-width: 450px) {
			font-size: 10px;
		}
	}
	&__properties {
		margin-top: 35px;
		@media(max-width: 1023px) {
			margin-top: 25px;
		}
		// @media(max-width: 900px) {
		// 	margin-top: 25px;
		// }
		@media(max-width: 450px) {
			margin-top: 0;
		}
	}
	&__price {
		margin-top: 50px;
		@media(max-width: 1023px) {
			margin-top: 40px;
		}
		// @media(max-width: 900px) {
		// 	margin-top: 40px;
		// }
		@media(max-width: 450px) {
			margin-top: 25px;
		}
	}
	&__btn {
		padding: 13px;
		margin-top: 40px;
		min-width: 	165px;
		@media(max-width: 1023px) {
			margin-top: 35px;
		}
		// @media(max-width: 900px) {
		// 	margin-top: 35px;
		// }
		@media(max-width: 450px) {
			margin-top: 20px;
		}
	}
}
.progress-bar {
	margin-top: 25px;
	@media(max-width: 1023px) {
		margin-top: 15px;
	}
	// @media(max-width: 900px) {
	// 	margin-top: 15px;
	// }
	&__title {
		display: block;
		font-size: 20px;
		text-transform: lowercase;
		letter-spacing: 0.22px;
		@media(max-width: 600px) {
			font-size: 14px;
		}
		@media(max-width: 450px) {
			font-size: 11px;
		}
	}
	&__line {
		position: relative;
		width: 100%;
		height: 5px;
		margin-top: 10px;
		border-radius: 10px;
		background-color: #f1f1f1;
		@media(max-width: 600px) {
			margin-top: 5px;
		}
	}
	&__line-inner {
		position: absolute;
		height: 5px;
		left: 0;
		top: 0;
		border-radius: 10px;
		&::after {
			position: absolute;
			content: '';
			top: -32px;
			right: 0;
			width: 31px;
			height: 13px;
			font-family: @ff;
			font-size: 16px;
			font-weight: 500;
			letter-spacing: 0.18px;
			@media(max-width: 600px) {
				top: -20px;
				font-size: 11px;
			}
			@media(max-width: 450px) {
				top: -17px;
				right: -19px;
				font-size: 10px;
			}
		}
	}
	&__line-inner-1 {
			.width-line-1();
			box-shadow: 0 4px 7px 1px rgba(0, 0, 0, 0.15);
			background: linear-gradient(-93deg, #f6888b 0%, #d25261 100%);
			&::after {
				content: '95%';
				color: #ec787e;
			}
		}
		&__line-inner-2 {
			.width-line-2();
			box-shadow: 0 4px 7px 1px rgba(0, 0, 0, 0.15);
			background: linear-gradient(-93deg, #88d9f6 0%, #1093e3 100%);
			&::after {
				content: '75%';
				color: #69c7f1;
			}
		}
		&__line-inner-3 {
			.width-line-3();
			box-shadow: 0 4px 7px 1px rgba(0, 0, 0, 0.15);
			background: linear-gradient(-93deg, #73e3a2 0%, #4fcf76 100%);
			&::after {
				content: '85%';
				color: #6de09b;
			}
		}
	.width-line-1 {
		width: 95%;
	}
	.width-line-2 {
		width: 75%;
	}
	.width-line-3 {
		width: 85%;
	}
}
.price {
	&__first {
		position: relative;
		margin-left: 35px;
		font-size: 36px;
		font-weight: bold;
		color: #302f2f;
		letter-spacing: 0.36px;
		@media(max-width: 1023px) {
			font-size: 32px;
		}
		// @media(max-width: 900px) {
		// 	font-size: 32px;
		// }
		@media(max-width: 450px) {
			margin-left: 18px;
			font-size: 18px;
		}
		sup {
			position: absolute;
			left: -32px;
			top: 15px;
			font-size: 28px;
			font-weight: 700;
			letter-spacing: 0.31px;
			@media(max-width: 1023px) {
				top: 13px;
				font-size: 24px;
			}
			// @media(max-width: 900px) {
			// 	top: 13px;
			// 	font-size: 24px;
			// }
			@media(max-width: 450px) {
				top: 7px;
				left: -17px;
				font-size: 15px;
			}
		}
	}
	&__last {
		position: relative;
		margin-left: 5px;
		margin-right: 12px;
		font-size: 25px;
		font-weight: 700;
		color: #666666;
		letter-spacing: 0.36px;
		@media(max-width: 1023px) {
			font-size: 22px;
		}
		// @media(max-width: 900px) {
		// 	font-size: 22px;
		// }
		@media(max-width: 450px) {
			margin-right: 5px;
			font-size: 13px;
		}
		sup {
			position: absolute;
			right: -10px;
			top: 10px;
			font-size: 16px;
			letter-spacing: 0.36px;
			@media(max-width: 450px) {
				right: -5px;
				top: 7px;
				font-size: 9px;
			}
		}
	}
}