.responses {
	padding: 0 160px;
	@media(max-width:1580px) {
		padding: 0 80px;
	}
	@media(max-width:600px) {
		padding: 0 40px;
	}
	@media(max-width:450px) {
		padding: 0;
	}
	&__slider {
		position: relative;
		margin-top: 100px;
		@media(max-width:767px) {
			margin-top: 70px;
		}
		@media(max-width:600px) {
			margin-top: 50px;
		}
		@media(max-width:450px) {
			margin-top: 35px;
		}
		.slick-list {
			width: 100%;
			overflow: hidden;
		}
		.slick-track{
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 100%;
		}
		.slick-slide {
			padding: 0 15px;
			&>div {
				height: 100%;
			}
		}
	}
}
.responses-slider {
		max-width: 1375px;
		padding: 0 80px;
		@media(max-width:1380px) {
			padding: 0 40px;
		}
		@media(max-width:450px) {
			padding: 0;
		}
		&__item {
			width: 100%;
			height: 100%;
			max-width: 300px;
		}
	}
.slide-wrap {
	height: 100%;
}
.response {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	height: 100%;
	max-width: 300px;
	margin: 0 auto;
	@media(max-width:450px) {
	max-width: 270px;
	}
	&__avatar {
		position: relative;
		margin-bottom: 20px;
		&-img {
			width: 123px;
			height: 123px;
			border-radius: 50%;
			overflow: hidden;
			@media(max-width:600px) {
				width: 110px;
				height: 110px;
			}
		}
		img {
			width: 100%;

		}
		&::after {
			position: absolute;
			content: '';
			bottom: -20px;
			right: 0;
			width: 37px;
			height: 38px;
			background-image: url('@{bg}responses/quote-icon.png');
			@media(max-width:600px) {
				bottom: -12px;
				width: 33px;
				height: 34px;
				background-size: contain;
			}
		}
	}
	&__text {
		display: flex;
    flex-direction: column;
    justify-content: space-between;
		max-width: 280px;
		height: 100%;
		margin-top: 25px;
		color: #a0a5a9;
		font-size: 21px;
		font-weight: 400;
		letter-spacing: 0.3px;
		text-align: center;
		@media(max-width:1200px) {
			margin-top: 20px;
			font-size: 18px;
		}
		@media(max-width:450px) {
			font-size: 13px;
		}
	}
	&__author {
		display: block;
		margin-top: 20px;
		color: #4290f2;
		font-size: 19px;
		font-weight: 500;
		letter-spacing: 0.27px;
		@media(max-width:1200px) {
			margin-top: 15px;
			font-size: 15px;
		}
		@media(max-width:450px) {
			font-size: 15px;
		}
	}
	&__player {
		margin-top: 25px;
	}
}
.player-panel {
	position: relative;
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	padding: 15px 22px;
	border-radius: 13px;
	background-color: #e4eaf3;
	@media(max-width:1200px) {
		padding-top: 13px 20px;
	}
	&__play {
		display: flex;
		align-items: center;
		margin-right: 5px; 
	}
	&__play-start,
	&__play-stop,
	&__play-volume {
		display: flex;
		align-items: center;
		padding: 0;
		cursor: pointer;
		border: none;
		background-color: transparent;
		&:hover {
			opacity: 0.7;
		}
	}
	&__play-start,
	&__play-stop {
		margin-right: 5px;
	}
	&__play-volume {
		margin-left: 5px;
	}
	&__btn-hidden {
		display: none;
	}
	&__progress-bar {
		position: relative;
		width: 100%;
		max-width: 170px;
		height: 5px;
		border-radius: 3px;
		background-color: #acabc0;
		&-line {
			position: absolute;
			width: 40px;
			height: 5px;
			top: 0;
			left: 0;
			border-radius: 3px;
			background-color: #525a68;
			&::after {
				position: absolute;
				content: '';
				width: 13px;
				height: 13px;
				top: 50%;
				right: 0;
				transform: translateY(-50%);
				border-radius: 50%;
				background-color: #525a68;
			}
		}
	}
	&__volume {
		position: relative;
		z-index: 2;
		&-panel {
			position: absolute;
			top: 0;
			right: 0;
			display: flex;
			display: none;
			justify-content: center;
			align-items: center;
			width: 164px;
			height: 49px;
			border-radius: 13px;
			background-color: #d9e1ee;
		}
		&-line {
			position: relative;
			width: 71px;
			height: 5px;
			overflow: hidden;
			border-radius: 3px;
			background-color: #acabc0;
			z-index: 1;
			&::before {
				position: absolute;
				content: '';
				top: 0;
				left: 0;
				width: 29px;
				height: 5px;
				border-radius: 3px;
				background-color: #525a68;
			}
		}
	}
}
.responses-arrow {
	position: absolute;
	width: 265px;
	height: 105px;
	font-size: 0;
	border:none;
	cursor: pointer;
	background-color: transparent;
	background-color: #356dba;
	outline: none;
	@media(max-width:1580px) {
		width: 160px;
	}
	@media(max-width:1380px) {
		width: 100px;
		height: 80px;
	}
	@media(max-width:600px) {
		width: 50px;
		height: 60px;
	}
	img {
		@media(max-width:600px) {
			width: 13px;
		}
	}
	&:hover {
		background-color: #477fcb;
	}
	&__right {
		right: -264px;
		top: 50%;
		transform: translateY(-50%);
		padding-left: 30px;
		text-align: left;
		@media(max-width:1580px) {
			right: -130px;
		}
		@media(max-width:1380px) {
			right: -80px;
		}
		@media(max-width:600px) {
			top: 19%;
			right: -40px;
			padding-left: 17px;
		}
		@media(max-width:450px) {
			right: 0;
		}
	}
	&__left {
		left: -264px;
		top: 50%;
		transform: translateY(-50%);
		padding-right: 30px;
		text-align: right;
		@media(max-width:1580px) {
			left: -130px;
		}
		@media(max-width:1380px) {
			left: -80px;
		}
		@media(max-width:600px) {
			left: -40px;
			top: 19%;
			padding-right: 17px;
		}
		@media(max-width:450px) {
			left: 0;
		}
	}
}
