.footer {
	min-height: 853px;
	&__map {
		position: relative;
		min-height: 853px;
		background: url('@{bg}contacts/map.jpg')no-repeat center;
		@media(max-width:767px) {
			min-height: 950px;
		}
	}
	&__social-btn {
		margin-top: 50px;
		@media(max-width:1023px) {
			margin-top: 30px;	
		}
		@media(max-width:600px) {
			margin-top: 25px;
		}
	}
	&__form {
		text-align: right;
		margin-top: 25px;
		@media(max-width:600px) {
			margin-top: 10px;
			text-align: center;
		}
	}
	&__input::placeholder {
		color: #d9e1ee;
	}
	&__form-btn {
		margin-top: 35px;
		color: #5686c8;
		background-color: #dfe9fa;
		@media(max-width:600px) {
			margin-top: 30px;
		}
		&__hover {
			background-color:	#d8e2f2;
		}
	}
}
.contacts {
	position: absolute;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	width: 40%;
	max-width: 768px;
	top: 70px;
	left: 0;
	padding: 5px 135px 30px;
	padding-left: 35px;
	color: #d9e1ee;
	border-top-right-radius: 13px;
	border-bottom-right-radius: 13px;
	background: linear-gradient(to top, rgba(53, 125, 199, 0.95) 0%, rgba(59, 105, 187, 0.95) 100%);
	z-index: 1;
	@media(max-width:1350px) {
		padding: 5px 30px 30px;
	}
	@media(max-width:1023px) {
		width: 50%;
	}
	@media(max-width:767px) {
		top: 40px;
		width: 70%;
	}
	@media(max-width:600px) {
		width: 100%;
		border-radius: 0;
	}
	&__inner {
		max-width: 370px;
		@media(max-width:1350px) {
			margin: 0 auto;
		}
	}
	&__item {
		width: 100%;
		max-width: 370px;
		margin-top: 45px;
		@media(max-width:1023px) {
			margin-top: 30px;
		}
		@media(max-width:600px) {
			margin-top: 25px;
		}
	}
}
.contact {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	&__type {
		margin-right: 20px;
		font-size: 25px;
		font-weight: 500;
		@media(max-width:1023px) {
			font-size: 20px;
		}
		@media(max-width:600px) {
			font-size: 17px;
		}
	}
	&__text {
		display: flex;
		flex-direction: column;
		width: 100%;
		max-width: 230px;
		padding-top: 4px;
		font-size: 22px;
		font-weight: 400;
		@media(max-width:1023px) {
			font-size: 18px;
		}
		@media(max-width:600px) {
			max-width: 160px;
			padding-top: 2px;
			font-size: 15px;
		}
	}
}
.social-btn {
	display: flex;
	margin-right: -19px;
	&__item {
		margin-right: 19px;
	}
	&__link {
		display: block;
		width: 45px;
		height: 45px;
		font-size: 0;
		border-radius: 50%;
		@media(max-width:600px) {
			width: 31px;
			height: 31px;
			background-size: contain;
		}
		&--vk {
			background: url('@{bg}contacts/vk-logo.png') no-repeat center;
		}
		&--tw {
			background: url('@{bg}contacts/twitter-logo.png') no-repeat center;
		}
	}
}
[class*="ymaps-2"][class*="-ground-pane"] {
	filter: grayscale(1);
	-ms-filter: grayscale(1);
	-webkit-filter: grayscale(1);
	-moz-filter: grayscale(1);
	-o-filter: grayscale(1);
}