.title {
	position: relative;
	font-family: @ff2;
	font-size: 50px;
	font-weight: 700;
	color: #3e5564;
	letter-spacing: 0.53px;
	line-height: 1.1;
	text-align: center;
	@media(max-width:900px) {
		font-size: 40px;
	}
	@media(max-width:450px) {
		font-size: 25px;
	}
	span {
		padding-left: 7px;
		color: #4290f2;
	}
	
}