.hero {
	&__container {
		display: flex;
		margin: 0 auto;
		padding: 0;
		padding-left: 155px;
		padding-right: 70px;
		@media(max-width: 1600px) {
			padding-left: 70px;
			padding-right: 30px;
		}
		@media(max-width: 1280px) {
			padding-left: 40px;
			padding-right: 30px;
		}
		@media(max-width: 950px) {
			width: 100%;
			padding: 0;
		}
	}
	&__left-block {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		width: 45%;
		margin-top: 85px;
		color: #ffffff;
		@media(max-width: 1280px) {
			width: 45%;
		}
		@media(max-width: 1140px) {
			width: 70%;
		}
		@media(max-width: 950px) {
			width: 100%;
			max-width: 650px;
			margin: 0 auto;
			padding: 0 20px;
			margin-top: 25px;
		}
		@media(max-width: 450px) {
			padding: 0;
		}
		&-wrap {
			display: flex;
			flex-direction: column;
			align-items: flex-end;
			@media(max-width: 450px) {
				padding: 0 50px;
			}
		}
	}
	&__right-block {
		display: flex;
		justify-content: center;
		text-align: center;
		margin: 35px -30px;
		margin-bottom: 0;
		@media(max-width: 950px) {
			display: none;
		}
	}
	&__mobile-img {
		display: none;
		width: 100%;
		justify-content: center;
		text-align: center;
		@media(max-width: 950px) {
			display: flex;
		}
		img {
			width: 320px;	
		}
	}
	&__promo-text {
		width: 90%;
		margin-top: 25px;
		font-family: @ff;
		font-size: 36px;
		color: rgba(255, 255, 255, .7);
		line-height: 1;
		font-weight: 500;
		text-align: right;
		letter-spacing: 0.49px;
		@media(max-width: 1280px) {
			margin-top: 20px;
			font-size: 28px;
		}
		@media(max-width: 450px) {
			margin-top: 10px;
			font-size: 14px;
		}
	}
	&__discr {
		margin: 0;
		margin-top: 20px;
		font-family: @ff;
		font-size: 21px;
		line-height: 1.1;
		font-weight: 500;
		letter-spacing: 0.28px;
		@media(max-width: 1280px) {
			margin-top: 15px;
			font-size: 18px;
		}
		@media(max-width: 450px) {
			margin-top: 10px;
			font-size: 12px;
		}
		
	}
	&__action-block {
		margin-top: 30px;
		@media(max-width: 950px) {
			margin-top: 15px;
		}
	}
}
.general-title {
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-end;
	font-family: @ff2;
	font-weight: bold;
	font-size: 100px;
	line-height: 0.8;
	letter-spacing: 0.81px;
	@media(max-width: 1280px) {
		font-size: 80px;
	}
	@media(max-width: 1200px) {
		max-width: 480px;
		font-size: 60px;
	}
	@media(max-width: 450px) {
		max-width: 310px;
		font-size: 36px;
	}
	&__first-text {
		display: inline-block;
		padding-right: 55px;
		@media(max-width: 1280px) {
			padding-right: 45px;
		}
	}
}
.action {
	display: flex;
	justify-content: space-between;
	width: 100%;
	margin-left: -5px;
	@media(max-width: 1200px) {
		flex-wrap: wrap;
		justify-content: center;
	}
	@media(max-width: 950px) {
		flex-wrap: nowrap;
		justify-content: space-between;
	}
	@media(max-width: 650px) {
		flex-wrap: wrap;
		justify-content: center;
	}
	@media(max-width: 450px) {
		padding: 0 10px;
	}
	&__wrap {
		display: flex;
		justify-content: space-between;
		@media(max-width: 1200px) {
		 width: 100%;
		}
	}
	&__item {
		width: 120px;
		margin: 10px;
		font-family: @ff;
		font-size: 21px;
		font-weight: 400;
		text-align: center;
		@media(max-width: 1280px) {
			margin: 10px 5px;
		}
		@media(max-width: 950px) {
			margin: 0 5px;
		}
		@media(max-width: 450px) {
			width: 110px;
		}
	}
	&__discr {
		margin-top: 10px;
		@media(max-width: 950px) {
			margin-top: 5px;
		}
		@media(max-width: 450px) {
			font-size: 14px;
			line-height: 1;
		}
	}
	&__block-btn {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-left: 20px;
		@media(max-width: 1200px) {
			margin-top:20px;	
		}
		
	}
	&__btn {
		@media(max-width: 450px) {
			padding: 10px 18px;
			font-size: 13px;
		}
	}
}
