.header {
	position: relative;
	max-width: 1920px;
	margin: 0 auto;
	min-height: 1061px;
	// padding-bottom: 70px;
	// margin-bottom: 200px;
	background:url('@{bg}bg-logo.png') no-repeat 0 0, url('@{bg}bg-header.jpg') no-repeat center/cover;
	@media(max-width:1680px) {
		background:url('@{bg}bg-logo.png') no-repeat -80px 0, url('@{bg}bg-header.jpg') no-repeat center/cover;
	}
	@media(max-width:1540px) {
		background:url('@{bg}bg-logo.png') no-repeat -135px 0, url('@{bg}bg-header.jpg') no-repeat center/cover;
	}
	@media(max-width:1380px) {
		background: url('@{bg}bg-header.jpg') no-repeat center/cover;
	}
	@media(max-width:1280px) {
		background:url('@{bg}bg-logo-mobile.png') no-repeat left top / 295px, url('@{bg}bg-header.jpg') no-repeat center/cover;
	}
	@media(max-width:1024px) {
		background:url('@{bg}bg-logo-mobile.png') no-repeat left top / 205px, url('@{bg}bg-header.jpg') no-repeat center/cover;
	}
	@media(max-width:450px) {
		background:url('@{bg}bg-logo-mobile.png') no-repeat left top / 175px, url('@{bg}bg-header.jpg') no-repeat center/cover;
	}
