	&__top-line {
		display: flex;
		justify-content: space-between;
		align-items: center;
		max-width: 1715px;
		margin: 0 auto;
		padding: 40px;
		@media(max-width:1280px) {
			padding-left: 25px;
			padding-right: 25px;
		}
		@media(max-width:1024px) {
			padding: 25px 25px;
		}
		@media(max-width:768px) {
			padding: 25px 20px;
		}
	}
	&__logo {
		position: relative;
		margin-right: 35px;
		@media(max-width:450px) {
			margin-right: 15px;
		}
	}
	&__top-menu {
		display: flex;
		align-items: flex-start;
	}
	&__phone {
		position: relative;
		min-width: 132px;
		padding-top: 8px;
		padding-left: 35px;
		margin-left: 40px;
		font-family: "Proxima Nova", sans-serif;
		color: #ffffff;
		font-size: 20px;
		font-weight: 700;
		letter-spacing: 0.6px;
		@media(max-width:1540px) {
			margin-left: 40px;
		}
		@media(max-width:450px) {
			font-size: 11px;
			min-width: 95px;
			margin-left: 15px;
		}
		&::before {
			content: '';
			position: absolute;
			bottom: 2px;
			left: 0;
			width: 25px;
			height: 25px;
			background: url('@{bg}tel_icon.png') no-repeat center/cover;
			@media(max-width:450px) {
				width: 13px;
				height: 13px;
			}
		}
	}
}
.header-form {
	position: absolute;
	bottom: -70px;
	left: 30%;
	transform: translateX(-50%);
	width: 307px;
	padding: 25px;
	padding-top: 35px;
	font-family: @ff;
	border-radius: 16px;
	background: #ffffff url('@{bg}cat-form.png') no-repeat 10px bottom;
	box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.14);
	@media(max-width: 1280px) {
		bottom: 25px;
		left: 25%;
	}
	@media(max-width: 950px) {
		bottom: -60px;
		left: 40%;
	}
	@media(max-width:650px) {
		bottom: -105px;
		left: 50%;
	}
	@media(max-width:450px) {
		bottom: 30px;
		width: 272px;
		padding: 20px;
		padding-top: 30px;
	}
	&::after {
		position: absolute;
		content: '';
		top: 9px;
		right: 9px;
		width: 19px;
		height: 19px;
		border-radius: 50%;
		background-color: #4478d0;
		@media(max-width:450px) {
			top: 12px;
			right: 12px;
		}
	}
	&__title {
		color: #4290f2;
		font-size: 19px;
		font-weight: 700;
		letter-spacing: 0.27px;
		@media(max-width:450px) {
			font-size: 15px;
		}
	}
	&__text {
		margin-top: 5px;
		color: #626262;
		font-size: 16px;
		font-weight: 500;
		font-style: italic;
		letter-spacing: 0.23px;
		@media(max-width:450px) {
			font-size: 14px;
		}
	}
	&__form {
		text-align: right;
		input {
			width: 100%;
			margin-top: 17px;
			padding: 8px 11px;
			border-radius: 14px;
			border: 1px solid #c0c0c0;
			outline: none;
			&::placeholder {
				font-family: @ff;
				font-size: 19px;
				font-weight: 300;
				font-style: italic;
				color: #aeaeae;
				letter-spacing: 0.27px;
			}
			&:focus::-webkit-input-placeholder{
				color: transparent
			}
			&:focus::-moz-placeholder{
				color: transparent
			}
			&:focus:-moz-placeholder{
				color: transparent
			}
			&:focus:-ms-input-placeholder {
				color: transparent
			}
		}
	}
	&__btn {
		margin-top: 35px;
		margin-left: 25px;
		cursor: pointer;
		@media(max-width:450px) {
			padding: 12px;
			font-size: 12px;
		}
	}
}