.link-btn {
	display: inline-block;
	padding: 15px;
	min-width: 135px;
	vertical-align: top;
	color: #ffffff;
	font-size: 0;
	border: none;
	background-color: transparent;
	font-size: 18px;
	font-weight: 700;
	text-align: center;
	text-decoration: none;
	border-radius: 4px;
	background-color: #4290f2;
	outline: none;
	transition: 0.3s linear;
	@media(max-width: 450px) {
		font-size: 10px;
		padding: 7px;
		min-width: 88px;
	}
	&:hover {
		background-color: #3379cf;
	}
	&--bg-white {
		background:#ffffff;
		color: #4377d0;
		&:hover	{
			background-color: #efeff1;
		}
	}
}