.mobail-menu {
	position: absolute;
	display: none;
	width: 100%;
	height: 100vh;
	top: 0;
	left: 0;
	overflow: auto;
	background: url('@{bg}bg-ball1.png') no-repeat 45px 271px,url('@{bg}bg-ball2.png') no-repeat right 115px,linear-gradient(to top, #557cad 0%, #557cad 8%, #315fc6 100%);
	opacity: 0;
	transition: 1s;
	z-index: 40;
	&__active {
		display: block;
		transition: 1s;
		opacity: 1;
	}
	&__logo {
		position: absolute;
		display: none;
		top: 0;
		left: 0;
		width: 175px;
		height: 90px;
		background: url('@{bg}bg-logo-mobile.png') no-repeat left top/175px;
		z-index: 41;
		@media(max-width:1280px) {
			display: block;
		}
		img {
			margin-top: 25px;
			margin-left: 20px;
		}
	}
	&__close {
		position: fixed;
		display: none;
		cursor: pointer;
		z-index: 41;
		@media(max-width:1280px) {
			display: block;
			top: 70px;
			right: 45px;
			width: 24px;
			height: 24px;
		}
		@media(max-width:1024px) {
			display: block;
			top: 41px;
			right: 23px;
			width: 14px;
			height: 14px;
		}
	}
}