.premium {
	max-width: 1270px;
	margin: 0 auto;
	padding: 0 15px;
	@media(max-width: 1200px) {
		padding: 0 25px;
	}
	@media(max-width: 900px) {
		padding: 0 45px;
	}
	@media(max-width: 450px) {
		padding: 0 20px;
	}
	&__title {
		@media(max-width: 600px) {
			display: flex;
			flex-direction: column;
			line-height: 1;
		}
	}
	&__discr {
		max-width: 500px;
		margin: 0 auto;
		margin-top: 70px;
		@media(max-width: 450px) {
			max-width: 250px;
		}
	}
	&__content {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-top: 110px;
		padding-bottom: 70px;
		@media(max-width: 1200px) {
			margin-top: 85px;
		}
		@media(max-width: 900px) {
			flex-direction: column;
			
		}
		@media(max-width: 600px) {
			margin-top: 55px;
		}
		@media(max-width: 450px) {
			margin-top: 45px;
		}
	}
	&__item {
		width: 50%;
		max-width: 575px;
		margin-bottom: 20px;
		@media(max-width: 900px) {
			width: 100%;
			margin-bottom: 0;
		}
		@media(max-width: 600px) {
			max-width: 450px;
		}
		@media(max-width: 450px) {
			max-width: 320px;
		}
	}
	&__item-img {
		float: left;
		width: 320px;
		height: 250px;
		shape-outside: ellipse(130px 130px at 45% 47%);
		@media(max-width: 1200px) {
			width: 250px;
			shape-outside: ellipse(110px 110px at 47% 44%);
		}
		@media(max-width: 600px) {
			width: 140px;
			height: 135px;
			shape-outside: ellipse(65px 65px at 47% 46%);
		}
		img {
			width: 100%;
		}
		//clip-path: circle(50%);
		//200 200
		//220ellipse(100px 100px at 47% 44%);
	}
	&__item-img-reverse {
		float: right;
		shape-outside: ellipse(130px 130px at 53% 47%);
		//clip-path: circle(50%);
		@media(max-width: 1200px) {
			width: 240px;
			shape-outside: ellipse(110px 110px at 50% 44%);
		}
		@media(max-width: 600px) {
			shape-outside: ellipse(65px 65px at 53% 46%);
		}
	}
	&__img-container {
		width: 208px;
		height: 208px;
		border-radius: 50%;
		overflow: hidden;
		box-shadow: 0 20px 40px #d7e1e9;
		@media(max-width: 1200px) {
			width: 200px;
			height: 200px;
		}
		@media(max-width: 600px) {
			width: 114px;
			height: 114px;
		}
		&--reverse {
			float: right;
		}
	}
	&__item-text {
		padding-top: 30px;
		font-size: 20px;
		font-weight: 400;
		line-height: 27px;
		color: #7c7b91;
		letter-spacing: 0.31px;
		@media(max-width: 1200px) {
			font-size: 18px;
		}
		@media(max-width: 600px) {
			padding-top: 20px;
			font-size: 15px;
		}
		@media(max-width: 450px) {
			padding-top: 15px;
			font-size: 11px;
			line-height: 1.3;
		}
	}
	&__item-text-rev {
		text-align: right;
	}
	&__item-order1 {
		@media(max-width: 900px) {
			order: 1;
		}
	}
	&__item-order2 {
		@media(max-width: 900px) {
			order: 3;
		}
	}
	&__item-order3 {
		@media(max-width: 900px) {
			order: 2;
			align-self: flex-end;
		}
	}
	&__item-order4 {
		@media(max-width: 900px) {
			order: 4;
			align-self: flex-end;
		}
	}
}