.instal {
	max-width: 1450px;
	padding: 0 20px;
	margin: 0 auto;
	padding-top: 90px;
	padding-bottom: 100px;
	@media(max-width: 600px) {
		padding-top: 75px;
		padding-bottom: 60px;
	}
	@media(max-width: 450px) {
		padding-top: 65px;
		padding-bottom: 35px;
	}
	&__discr {
		max-width: 650px;
		margin-left: auto;
		margin-right: auto;
	}
	&__content {
		display: flex;
		margin-top: 140px;
		@media(max-width: 1365px) {
			margin-top: 110px;
		}
		@media(max-width: 1200px) {
			flex-wrap: wrap;
			margin-top: 50px;
		}
		@media(max-width: 900px) {
			margin-top: 30px;
		}
		@media(max-width: 450px) {
			margin-top: 30px;
		}
	}
	&__content-img {
		max-width: 510px;
		padding-top: 30px;
		@media(max-width: 1200px) {
			display: flex;
			justify-content: center;
			width: 100%;
			max-width: 100%;
			padding-top: 0;
			order: 1;
		}
		img {
			width: 100%;
			@media(max-width: 1200px) {
				max-width: 320px;
			}
			// @media(max-width: 900px) {
			// 	max-width: 320px;
			// }
			@media(max-width: 600px) {
				max-width: 245px;
			}
			@media(max-width: 450px) {
				max-width: 210px;
			}
		}
	}
	&__content-col {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		max-width: 432px;
		padding-bottom: 30px;
		@media(max-width: 1365px), (orientation: landscape) {
			max-width: 350px;
		}
		@media(max-width: 1200px) {
			width: 48%;
			max-width: 100%;
			min-width: auto;
			margin: 0 1%;
			margin-top: 15px;
			order: 2;
		}
		@media(max-width: 600px) {
			width: 100%;
		}
		@media(max-width: 450px) {
			margin-top: 0;
		}
	}
	&__content-right-col {
		@media(max-width: 1200px) {
			order: 3;
		}
		@media(max-width: 600px) {
			display: none;
		}
	}
	&__item {
		font-family: @ff;
		text-align: center;
		margin-bottom: 40px;
		@media(max-width: 1365px) {
			margin-bottom: 20px;
		}
		@media(max-width: 600px) {
			width: 330px;
			margin-left: auto;
			margin-right: auto;
		}
		@media(max-width: 450px) {
			max-width: 230px;
			margin-top: 25px;
		}
		&-icon {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 81px;
			height: 81px;
			margin: 0 auto;
			border-radius: 50%;
			background-color: #4290f3;
			@media(max-width: 900px) {
				width: 70px;
				height: 70px;
			}
			@media(max-width: 450px) {
				width: 45px;
				height: 45px;
			}
			img {
				@media(max-width: 900px) {
					width: 40px;
				}
				@media(max-width: 450px) {
					width: 30px;
				}
			}
		}
		&-title {
			margin-top: 25px;
			font-size: 23px;
			font-weight: 700;
			color: #485c6a;
			letter-spacing: 0.33px;
			@media(max-width: 900px) {
				font-size: 18px;
			}
			@media(max-width: 450px) {
				margin-top: 15px;
				font-size: 13px;
			}
			span {
				margin-left: 5px;
				color: #4290f3;
			}
		}
		&-discr {
			margin-top: 25px;
			color: #485c6a;
			font-size: 19px;
			font-weight: 400;
			letter-spacing: 0.27px;
			@media(max-width: 1365px) {
				margin-top: 15px;
			}
			@media(max-width: 900px) {
				font-size: 14px;
			}
			@media(max-width: 450px) {
				margin-top: 8px;
				font-size: 11px;
			}
		}
	}

}
.bg-cont {
	position: relative;
	&::after,
	&::before {
		content:'';
		position: absolute;
		height: 85px;
		background: #fff;
		width: 100%;
		display: block;
		@media(max-width: 600px) {
			height: 60px;
		}
		@media(max-width: 450px) {
			height: 40px;
		}
	}
	&::before {
		top: 0;
		left: 0;
		background: linear-gradient(to left top, transparent 50%, #ffffff 50%);
	}
	&::after {
		bottom: 0;
		right: 0;
		background: linear-gradient(to right bottom, transparent 50%, #ffffff 50%);
	}
}