* {
	box-sizing: border-box;
}
body {
	position: relative;
	font-family: @ff;
	font-weight: normal;
	font-size: 16px;
	width: 100%;
	min-width: 320px;
	height: 100%;
	margin: 0;
	line-height: 1.2;
}
.lock {
	overflow: hidden;
}
h1, h2, h3, h4, h5, h6, ul, p {
	padding: 0;
	margin: 0;
	font-weight: normal;
}
ul {
	padding: 0;
	margin: 0;
	list-style-type: none;
}
.link {
	display: inline-block;
	text-decoration: none;
	cursor: pointer;
	outline: none;
	border: none;
	font-size: 0;
	font-size: 18px;
	vertical-align: top;
	color: #ffffff;
}
.btn {
	display: inline-block;
	min-width: 135px;
	padding: 15px;
	font-size: 0;
	font-family: @ff;
	font-size: 18px;
	font-weight: 700;
	color: #ffffff;
	border: none;
	border-radius: 4px;
	background-color: transparent;
	background-color: #4290f2;
	cursor: pointer;
	&:hover {
		background-color: #3379cf;
	}
	&--bg-white {
		background:#ffffff;
		color: #4377d0;
		&:hover {
			background-color: #efeff1;
		}
	}
}
.wrapper {
	width: 100%;
	max-width: 1920px;
	margin: 0 auto;
	position: relative;
	padding: 0 104px;
	@media(max-width:1580px) {
		padding: 0 50px;
	}
	@media(max-width:1380px) {
		padding: 0;
	}
}
.container {
	width: 100%;
	max-width: 1170px;
	margin: 0 auto;
	padding-right: 15px;
	padding-left: 15px;
	@media(max-width: 1200px) {
		max-width: 1030px;
	}
	@media(max-width: 770px) {
		max-width: 600px;
	}
}
.flex-center {
	display: flex;
	justify-content: center;
	align-items: center;
}
.img-responsive {
	width: 100%;
	height: auto;
}
input {
	&:focus::-webkit-input-placeholder{
		color: transparent
	}
	&:focus::-moz-placeholder{
		color: transparent
	}
	&:focus:-moz-placeholder{
		color: transparent
	}
	&:focus:-ms-input-placeholder {
		color: transparent
	}
}
.clearfix::after { 
	content: "";
	display: block;
  clear: both;
}
.hidden {
	display: none;
}
.overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: #f8fafc;
	z-index: 100;
}