.adventages {
	margin: 0 auto;
	width: 100%;
	max-width: 1406px;
	padding: 0 50px;
	@media(max-width:1380px) {
		padding: 0;
	}
	&__title {
		padding-right: 65px;
		@media(max-width: 600px) {
			padding-right: 35px;
		}
		@media(max-width: 450px) {
			padding-right: 20px;
		}
	}
	&__container {
		position: relative;
		padding: 85px 0;
		@media(max-width: 450px) {
			padding: 40px 0;
		}
	}
	&__container-bg {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 985px;
		height: 988px;
		margin: 0 auto;
		background: url('@{bg}cube1.png') no-repeat bottom 157px right 279px, url('@{bg}cube2.png') no-repeat 57px 79px ,url('@{bg}cube3.png') no-repeat top 10px right 159px , url('@{bg}circle-window.png') no-repeat center 5px;
		background-size: auto, auto, auto, 983px;
		@media(max-width: 1366px) {
			width: 500px;
			height: 500px;
			margin: 0 auto;
			background: url('@{bg}cube1-mob.png') no-repeat bottom 35px right 67px, url('@{bg}cube2-mob.png') no-repeat 57px top ,url('@{bg}cube3-mob.png') no-repeat top 10px right 35px , url('@{bg}circle-window.png') no-repeat center 4px;
			background-size: auto, auto, auto, 500px;
		}
		@media(max-width: 500px) {
			width: 100%;
		}
	}
	&__items {
		position: relative;
		width: 563px;
		height: 563px;
		margin: 0 auto;
		@media(max-width: 1366px) {
			width: 286px;
			height: 286px;
			margin: 0 auto;

		}
		&::after {
			position: absolute;
			content: '';
			bottom: 125px;
			left: 0;
			width: 65px;
			height: 60px;
			background: transparent url('@{bg}win-cube.png') no-repeat center center/contain;
			@media(max-width: 1366px) {
				bottom: 57px;
				left: -17px;
				background: transparent url('@{bg}win-cube-mob.png') no-repeat center center/contain;
			}
		}
		&-img {
			width: 100%;
			height: 100%;
			border-radius: 50%;
			overflow: hidden;
			background: url('@{bg}win-cube1.png') no-repeat 302px 20px, url('@{bg}win-cube2.png') no-repeat 430px 235px,linear-gradient(-99deg, #3ba0ec 0%, #3ba0ec 32%, #3d73ce 63%, #3d73ce 100%);
			@media(max-width: 1366px) {
				background: url('@{bg}win-cube-mob1.png') no-repeat 142px 4px, url('@{bg}win-cube-mob2.png') no-repeat 209px 120px,linear-gradient(-99deg, #3ba0ec 0%, #3ba0ec 32%, #3d73ce 63%, #3d73ce 100%);
			}
		}
		img {
			margin-top: 73px;
			@media(max-width: 1366px) {
				margin-top: 40px;
			}
		}
	}
	&__item {
		position: absolute;
		font-family: @ff;
		font-size: 20px;
		font-weight: 500;
		line-height: 1;
		letter-spacing: 0.28px;
		text-align: center;
		color: #7c7b91;
		@media(max-width: 900px) {
			font-size: 16px;
			line-height: 1.1;
		}
		@media(max-width: 450px) {
			font-size: 10px;
			line-height: 1.1;
		}
	}
	&__item-enrgy {
		top: 25px;
		left: -362px;
		@media(max-width: 1366px) {
			top: -67px;
			left: -279px;
		}
		@media(max-width: 900px) {
			top: -86px;
			left: -11px;
		}
		@media(max-width: 450px) {
			top: -80px;
			left: 17px;
		}
	}
	&__item-prem {
		top: -194px;
		left: 37px;
		@media(max-width: 1366px) {
			top: -175px;
			left: 20px;
		}
		@media(max-width: 900px) {
			top: -156px;
			left: 87px;
		}
		@media(max-width: 450px) {
			top: -126px;
			left: 91px;
		}
		
	}
	&__item-sound {
		top: 168px;
		right: -354px;
		@media(max-width: 1366px) {
			top: 124px;
			right: -290px;
		}
		@media(max-width: 900px) {
			top: -52px;
			right: -25px;
		}
		@media(max-width: 450px) {
			top: -58px;
			right: 20px;
		}
	}
	&__item-color {
		bottom: -76px;
		right: -352px;
		@media(max-width: 1366px) {
			bottom: -110px;
			right: -228px;
		}
		@media(max-width: 900px) {
			bottom: -135px;
			right: 22px;
		}
		@media(max-width: 450px) {
			bottom: -116px;
	 		right: 55px;
		}
		
	}
	&__item-heat {
		top: 451px;
		left: -366px;
		@media(max-width: 1366px) {
			top: 191px;
			left: -266px;
		}
		@media(max-width: 900px) {
			top: 343px;
			left: -43px;
		}
		@media(max-width: 450px) {
			top: 343px;
			left: 4px;
		}
		
	}
	
	&__discr {
		position: relative;
		float: left;
		text-align: center;
		max-width: 195px;
	}
}
.adventages__icon-discr {
	position: absolute;
	top: 125%;
	left: 50%;
	&::before {
		position: absolute;
		content: '';
		width: 10px;
		height: 10px;
		border-radius: 50%;
		background-color: #4290f2;
		@media(max-width: 450px) {
			width: 6px;
			height: 6px;
		}
	}
	&::after {
		position: absolute;
		content: '';
		width: 50px;
		height: 50px;
		@media(max-width: 1366px) {
			width: 25px;
			height: 25px;
			background-size: cover;
		}
	}
}
.adventages__icon-discr-enrgy {
	top: 125%;
	left: 50%;
	width: 205px;
	height: 160px;
	border-left: 2px solid #4290f2;
	border-bottom: 2px solid #4290f2;
	@media(max-width: 1366px) {
		width: 160px;
		height: 100px;
	}
	@media(max-width: 900px) {
		left: 26%;
		width: 30px;
		height: 50px;
		border-left: 2px solid #4290f2;
		border-top: 2px solid #4290f2;
		border-bottom: none;
	}
	&::before {
		top: -5px;
		left: -6px;
		@media(max-width: 900px) {
			left: auto;
			top: -6px;
			right: -6px;
		}
		@media(max-width: 450px) {
			top: -4px;
			right: -5px;
		}
	}
	&::after {
		bottom: -24px;
		right: -48px;
		background-image: url('@{bg}icon-energy.png');
		@media(max-width: 1366px) {
			bottom: -12px;
			right: -25px;
		}
		@media(max-width: 900px) {
			bottom: -25px;
			left: -12px;
		}
	}
}
.adventages__icon-discr-prem {
	width: 0;
	height: 110px;
	border-left: 2px solid #4290f2;
	border-bottom: 2px solid #4290f2;
	@media(max-width: 1366px) {
		height: 95px;
	}
	@media(max-width: 900px) {
		height: 80px;
	}
	@media(max-width: 450px) {
		top: 140%;
		height: 60px;
	}
	&::before {
		top: -5px;
		left: -6px;
		@media(max-width: 450px) {
			left: -4px;
		}
	}
	&::after {
		bottom: -50px;
		right: -25px;
		background-image: url('@{bg}icon-prem.png');
		@media(max-width: 1366px) {
			bottom: -25px;
			right: -12px;
		}
	}
}
.adventages__icon-discr-sound {
	top: -91px;
	left: -218px;
	width: 290px;
	height: 80px;
	border-right: 2px solid #4290f2;
	border-top: 2px solid #4290f2;
	@media(max-width: 1366px) {
		top: -76px;
		left: -146px;
		width: 217px;
		height: 68px;
	}
	@media(max-width: 900px) {
		top: 42px;
		left: 57px;
		width: 29px;
		height: 53px;
	}
	@media(max-width: 450px) {
		top: 28px;
		left: 43px;
		width: 19px;
		height: 40px;
	}
	&::before {
		bottom: -5px;
		right: -6px;
		@media(max-width: 900px) {
			bottom: auto;
			right: auto;
			top: -6px;
			left: -6px;
		}
		@media(max-width: 900px) {
			top: -4px;
		}
	}
	&::after {
		top: -25px;
		left: -50px;
		background-image: url('@{bg}icon-sound.png');
		@media(max-width: 1366px) {
			top: -12px;
			left: -25px;
		}
		@media(max-width: 900px) {
			top: auto;
			left: auto;
			bottom: -25px;
			right: -13px;
		}
	}
}
.adventages__icon-discr-color {
	top: -210px;
	left: -120px;
	width: 213px;
	height: 200px;
	border-right: 2px solid #4290f2;
	border-top: 2px solid #4290f2;
	@media(max-width: 1366px) {
		top: -100px;
		left: -66px;
		width: 151px;
		height: 90px;
	}
	@media(max-width: 900px) {
		top: -115px;
		left: 71px;
		width: 69px;
		height: 109px;
		border-top: none;
		border-bottom: 2px solid #4290f2;
	}
	@media(max-width: 450px) {
		top: -87px;
		left: 54px;
		width: 43px;
		height: 83px;
	}
	
	&::before {
		bottom: -5px;
		right: -6px;
		@media(max-width: 900px) {
			right: auto;
			left: -6px;
		}
		@media(max-width: 450px) {
			bottom: -4px;
		}
	}
	&::after {
		top: -25px;
		left: -50px;
		background-image: url('@{bg}icon-color.png');
		@media(max-width: 1366px) {
			top: -12px;
			left: -25px;
		}
		@media(max-width: 900px) {
			left: auto;
			top: -25px;
			right: -12px;
		}
	}
}
.adventages__icon-discr-heat {
	top: 125%;
	left: 50%;
	width: 454px;
	height: 91px;
	border-left: 2px solid #4290f2;
	border-bottom: 2px solid #4290f2;
	@media(max-width: 1366px) {
		width: 260px;
		height: 55px;
	}
	@media(max-width: 900px) {
		top: -175%;
		width: 35px;
		height: 50px;
		border-left: 2px solid #4290f2;
		border-top: 2px solid #4290f2;
		border-bottom: none;
	}
	@media(max-width: 450px) {
		top: -220%;
		width: 35px;
		height: 42px;
	}
	&::before {
		top: -5px;
		left: -6px;
		@media(max-width: 900px) {
			top: auto;
			bottom: -5px;
			left: -6px;
		}
		@media(max-width: 450px) {
			left: -4px;
		}
	}
	&::after {
		bottom: -24px;
		right: -48px;
		background-image: url('@{bg}icon-heat.png');
		@media(max-width: 1366px) {
			bottom: -12px;
			right: -25px;
		}
		@media(max-width: 900px) {
			top: -12px;
			bottom: auto;
		}
	}
}