.discr {
	position: relative;
	width: 100%;
	padding: 0;
	margin: 0;
	margin-top: 70px;
	font-family: @ff;
	font-size: 23px;
	font-weight: 400;
	text-align: center;
	letter-spacing: 0.33px;
	color: #5c5b5b;
	@media(max-width:900px) {
		font-size: 20px;
		margin-top: 45px;
	}
	@media(max-width:450px) {
		margin-top: 30px;
		font-size: 12px;
	}
	&::before {
		content:'';
		position: absolute;
		width: 80px;
		height: 2px;
		top: -30px;
		left: 50%;
		transform: translateX(-50%);
		background-color: #4290f2;
		@media(max-width:900px) {
			top: -20px;
		}
		@media(max-width:450px) {
			top: -15px;
		}
	}
}