.main {
	padding-top: 160px;
	@media(max-width: 450px) {
		padding-top: 30px;
	}
	&::after {
		position: absolute;
		content: '';
		width: 105px;
		height: 100%;
		top: 0;
		left: 0;
		background: url('@{bg}bg-left-line.png') repeat-y right/100%;
		z-index: 10;
		@media(max-width:1580px) {
			width: 50px;
		}
		@media(max-width:1380px) {
			display: none;
		}
	}
	&::before {
		position: absolute;
		content: '';
		width: 105px;
		height: 100%;
		top: 0;
		right: 0;
		background: url('@{bg}bg-right-line.png') repeat-y left/100%;
		z-index: 10;
		@media(max-width:1580px) {
			width: 50px;
		}
		@media(max-width:1380px) {
			display: none;
		}
	}
}
.services {
	max-width: 1220px;
	margin: 0 auto;
	padding: 0 20px;
	&__content {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		width: 100%;
		margin-top: 70px;
		@media(max-width: 1024px) {
			flex-wrap: wrap;
			padding: 0 40px; 
		}
		@media(max-width: 768px) {
			justify-content: center;
			margin-top: 30px;
		}
		@media(max-width: 450px) {
			margin-top: 0;
		}
	}
	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 28.5%;
		font-family: @ff;
		text-align: center;
		@media(max-width: 1024px) {
			width: 48%;
			margin-top: 15px;
		}
		@media(max-width: 768px) {
			width: 100%;
			max-width: 400px;
			margin: 15px;
		}
		@media(max-width: 450px) {
			max-width: 170px;
		}
	}
	&__img {
		border-radius: 26px;
		overflow: hidden;
	}
	&__item-title {
		margin-top: 30px;
		color: #7c7b91;
		font-size: 25px;
		font-weight: 700;
		letter-spacing: 0.36px;
		@media(max-width: 767px) {
			margin-top: 15px;
			font-size: 16px;
		}
		@media(max-width: 450px) {
			font-size: 14px;
		}
	}
	&__item-discr {
		margin-top: 20px;
		color: #5c5b5b;
		font-size: 22px;
		font-weight: 400;
		letter-spacing: 0.31px;
		@media(max-width: 767px) {
			margin-top: 10px;
			font-size: 20px;
		}
		@media(max-width: 450px) {
			font-size: 11px;
		}
	}
}
.service-title {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	padding-top: 25px;
	padding-bottom: 15px;
	font-family: @ff2;
	font-size: 60px;
	font-weight: bold;
	color: #4290f2;
	letter-spacing: 0.64px;
	@media(max-width: 900px) {
		font-size: 40px;
	}
	@media(max-width: 450px) {
		font-size: 26px;
	}
	&__text {
		position: relative;
		&::before {
			position: absolute;
			content: 'Наши';
			top: -38px;
			left: -49px;
			width: 100%;
			font-weight: normal;
			@media(max-width: 450px) {
				top: -20px;
				left: -25px;
			}
		}
	}
}
.adventages-s {
	margin-top: 140px;
	@media(max-width:900px) {
		margin-top: 80px;
	}
	@media(max-width:450px) {
		margin-top: 60px;
	}
}
.accessories-s {
	margin-top: 150px;
	@media(max-width: 1200px) {
		margin-top: 120px;
	}
	@media(max-width: 600px) {
		margin-top: 95px;
	}
}
.instalation-s {
	background-color: #f6f6ff;
}
.instalment-s {
	padding-top: 115px;
	@media(max-width: 1023px) {
		padding-top: 80px;
	}
	@media(max-width: 900px) {
		padding-top: 40px;
	}
}
.discounts-s {
	margin-top: 30px;
}
.responses-s {
	padding-bottom: 160px;
	@media(max-width:767px) {
		padding-bottom: 60px;
	}
}
.modal-discount {	
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
	max-width: 1250px;
	padding: 0 20px;
	z-index: 101;
	@media(max-width: 1280px) {
		max-width: 1140px;
	}
	@media(max-width: 900px) {
		max-width: 690px;
	}
	@media(max-width: 450px) {
		padding: 0;
	}
	&__wrap {
		position: relative;
		display: flex;
		box-shadow: 0 4px 51px 8px rgba(0, 0, 0, 0.1);
		border-radius: 11px;
		overflow: hidden;
		background-color: #ffffff;
		@media (max-width: 900px) {
			background: url('@{bg}discount/discount-open-mob.jpg') no-repeat center;
			background-size: cover
		}
		@media(max-width: 450px) {
			border-radius: 0;
		}
	}
	&__content {
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 50%;
		padding: 65px 80px 55px;
		z-index: 2;
		@media(max-width: 1280px) {
			padding: 50px 65px 40px;
		}
		@media(max-width: 1000px) {
			padding: 45px 35px 20px;
		}
		@media(max-width: 900px) {
			width: 100%;
		}
		@media(max-width: 450px) {
			padding: 30px 18px 30px;
		}
	}
	&__title {
		color: #3d82d8;
		font-size: 40px;
		font-weight: 500;
		@media(max-width: 1280px) {
			font-size: 34px;
		}
		@media(max-width: 450px) {
			font-size: 23px;
		}
	}
	&__subtitle {
		margin-top: 12px;
		color: #3a3a3a;
		font-size: 17px;
		font-weight: 400;
		font-style: italic;
		@media(max-width: 1280px) {
			margin-top: 8px;
			font-size: 14px;
		}
		@media(max-width: 450px) {
			font-size: 11px;
		}
	}
	&__text {
		position: relative;
		margin-top: 35px;
		padding-top: 20px;
		color: #5c5b5b;
		font-size: 16px;
		font-weight: 400;
		@media(max-width: 1280px) {
			margin-top: 25px;
			font-size: 13px;
		}
		@media(max-width: 450px) {
			margin-top: 20px;
			padding-top: 15px;
			font-size: 11px;
		}
		&::before {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			width: 80px;
			height: 2px;
			background-color: #4290f2;
		}
		p {
			margin-top: 20px;
			@media(max-width: 1280px) {
				margin-top: 12px;
			}
		}
	}
	&__btn-block {
		margin-top: 60px;
		@media(max-width: 1280px) {
			margin-top: 30px;
		}
	}
	&__btn {
		margin-left: 15px;
	}
	&--close {
		@media(max-width: 1280px) {
			margin-left: 0;
		}
		@media(max-width: 450px) {
			margin-left: 0;
		}
	}
	&__img {
		width: 50%;
		font-size: 0;
		@media(max-width: 900px) {
			display: none;
		}
		img {
			width: 100%;
			height: auto;
		}
	}
	&__overlay {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(248, 250, 252, 0.8);
		z-index: 1;
		@media(max-width: 900px) {
			display: block;
		}
	}
}
.btn-block {
	&__item {
		padding: 12px 17px;
		font-size: 16px;
		font-weight: 500;
		letter-spacing: 0.66px;
		@media(max-width: 450px) {
			margin-left: 8px;
			padding: 7px 8px;
			font-size: 11px;
		}
	}
	&__item--grey {
		background-color: #a0a0a0;
		&:hover {
			background-color: #abacae;
		}
	}
}