.instalment {
	&__content {
		display: flex;
		justify-content: center;
		width: 100%;
		max-width: 1205px;
		margin: 0 auto;
		margin-top: 75px;
		padding: 0 20px;
		padding-bottom: 170px;
		@media(max-width: 900px) {
			flex-direction: column;
			margin-top: 40px;
			padding-bottom: 120px;
			align-items: center;
		}
	}
	&__form {
		position: relative;
		width: 100%;
		max-width: 490px;
		padding: 50px 40px 30px 50px;
		border-radius: 13px;
		box-shadow: 0 4px 33px 5px rgba(0, 0, 0, 0.02);
		text-align: right;
		background: #ffffff url('@{bg}instalment/form-bg.png') no-repeat left bottom/contain;
		z-index: 2;
		@media(max-width: 1023px) {
			max-width: 420px;
			padding: 35px 30px 20px 35px;
		}
		@media(max-width: 450px) {
			max-width: 290px;
			padding: 30px 30px 18px 30px;
		}
	}
	&__form-inner {
		padding-right: 35px;
		text-align: left;
		@media(max-width: 1023px) {
			padding-right: 15px;
		}
		@media(max-width: 450px) {
			padding-right: 10px;
		}
	}
	&__form-btn {
		margin-top: 70px;
		@media(max-width: 450px) {
			margin-top: 40px;
		}
	}
	&__form-wrap {
		position: relative;
		min-width: 440px;
		margin-top: 50px;
		@media(max-width: 1023px) {
			min-width: 380px;
		}
		@media(max-width: 900px) {
			margin-top: 85px;
			order: 2;
		}
		@media(max-width: 450px) {
			min-width: auto;
			margin-top: 65px;
		}
		&::before {
			position: absolute;
			content: '';
			top: -50px;
			left: -120px;
			width: 400px;
			height: 215px;
			border-radius: 13px;
			background-color: #f6f6ff;
			z-index: 1;
			@media(max-width: 450px) {
				top: -30px;
				left: -70px;
				width: 234px;
				height: 126px;
			}
		}
	}
	&__conditions {
		width: 100%;
		max-width: 400px;
		margin-top: 50px;
		margin-left: 50px;
		@media(max-width: 900px) {
			margin-top: 0;
			margin-left: 0;
			order: 1;
		}
		@media(max-width: 450px) {
			max-width: 300px;
		}
	}
}
.form {
	&__title {
		color: #97a4b8;
		font-size: 26px;
		font-weight: 500;
		font-weight: 500;
		@media(max-width: 1023px) {
			font-size: 20px;
		}
		@media(max-width: 450px) {
			font-size: 15px;
		}
		&--sale {
			margin-left: 5px;
			font-size: 28px;
			@media(max-width: 1023px) {
				font-size: 22px;
			}
			@media(max-width: 450px) {
				font-size: 16px;
			}
		}
		&--perc {
			font-size: 34px;
			@media(max-width: 1023px) {
				font-size: 26px;
			}
			@media(max-width: 450px) {
				font-size: 20px;
			}
		}
		span {
			color: #3d82d8;
			font-weight: 700;
		}
	}
	&__btn {
		padding: 15px 20px;
		font-size: 20px;
		font-weight: 500;
		border-radius: 14px;
		background-color: #6e9cde;
		cursor: pointer;
		@media(max-width: 1023px) {
			padding: 12px 17px;
    	font-size: 18px;
		}
		@media(max-width: 450px) {
			padding: 10px;
    	font-size: 12px;
		}
		
		&:hover {
			background-color: #81a8e2; 
		}
	}
}
.input {
	position: relative;
	width: 100%;
	padding: 19px 64px;
	outline: none;
	border-radius: 14px;
	border: 2px solid #f1f0f6;
	@media(max-width: 1023px) {
		padding: 15px 64px;
	}
	@media(max-width: 450px) {
		padding: 10px 38px;
		font-size: 12px;
	}
	&::placeholder {
		font-family: @ff;
		font-size: 21px;
		font-weight: 400;
		font-style: italic;
		color: #acabc0;
		letter-spacing: 0.24px;
		@media(max-width: 1023px) {
			font-size: 18px;
		}
		@media(max-width: 450px) {
			font-size: 12px;
		}
	}
	&:focus::-webkit-input-placeholder{
		color: transparent
	}
	&:focus::-moz-placeholder{
		color: transparent
	}
	&:focus:-moz-placeholder{
		color: transparent
	}
	&:focus:-ms-input-placeholder {
		color: transparent
	}
}
.label {
	position: relative;
	display: inline-block;
	width: 100%;
	margin-top: 25px;
	@media(max-width: 450px) {
		margin-top: 15px;
	}
	&::before {
		position: absolute;
		content: '';
		top: 50%;
		left: 12px;
		transform: translateY(-50%);
		width: 37px;
		height: 37px;
		border-radius: 50%;
		overflow: hidden;
		z-index: 1;
		@media(max-width: 450px) {
			left: 8;
			width: 22px;
			height: 22px;
			background-size: contain;
		}
	}
	&__user::before {
		background-image: url('@{bg}instalment/user-icon.png');
	}
	&__phone::before {
		background-image: url('@{bg}instalment/phone-icon.png');
	}
}
.conditions {
	&__item {
		width: 100%;
		border-radius: 13px;
		@media(max-width: 450px) {
			min-height: 80px;
		}
		&--bg {
			background-color: #f6f6ff;
		}
	}
	&__item-plan {
		position: relative;
		display: flex;
		flex-direction: column;
		width: 100%;
		padding-left: 195px;
		padding-bottom: 12px;
		@media(max-width: 1023px) {
			padding-left: 150px;
		}
		@media(max-width: 450px) {
			padding-left: 115px;
		}
		&-top {
			font-size: 65px;
			font-weight: 700;
			color: #3d82d8;
			letter-spacing: 0.39px;
			@media(max-width: 1023px) {
				font-size: 55px;
			}
			@media(max-width: 450px) {
				font-size: 38px;
			}
		}
		&-bot {
			max-width: 150px;
			margin-top: 2px;
			font-size: 18px;
			font-weight: 700;
			color: #3e5564;
			@media(max-width: 1023px) {
				max-width: 130px;
				font-size: 15px;
			}
			@media(max-width: 450px) {
				max-width: 90px;
				font-size: 11px;
			}
		}
		&::before {
			position: absolute;
			content: '';
			left: 50px;
			top: 50%;
			transform: translateY(-50%);
			width: 93px;
			height: 93px;
			border-radius: 50%;
			overflow: hidden;
			box-shadow: 0 20px 40px #d7e1e9;
			@media(max-width: 1023px) {
				left: 30px;
				width: 80px;
				height: 80px;
			}
			@media(max-width: 450px) {
				width: 55px;
				height: 55px;
				background-size: 60%;
			}
		}
	}
	&__item-plan1::before {
		background: #3d82d8 url('@{bg}instalment/plan1-icon.png') no-repeat center;
	}
	&__item-plan2::before {
		background: #ffffff url('@{bg}instalment/plan2-icon.png') no-repeat center;
	}
	&__item-plan3::before {
		background: #3d82d8 url('@{bg}instalment/plan3-icon.png') no-repeat center;
	}
}