.discounts {
	&__discr {
		max-width: 650px;
		margin: 70px auto;
		margin-bottom: 0;
	}
	&__slider {
		margin-top: 70px;
		@media(max-width: 450px) {
			margin-top: 30px;
		}
		.slick-list {
			width: 100%;
			overflow: hidden;
		}
		.slick-track{
			display: flex;
			justify-content: space-between;
			align-items: flex-start;
			width: 100%;
			margin-bottom: 75px;
			padding-bottom: 40px;
			@media(max-width: 450px) {
				padding-bottom: 20px;
			}
		}
		.slick-slide {
			display: flex;
			justify-content: flex-end;
			margin: 0 10px;
			opacity: 0.6;
			overflow: hidden;
			border-radius: 11px;
			transition: 0.3s;
			&>div {
				height: 100%;
			}
		}
		.slick-center {
			position: relative;
			top: 40px;
			opacity: 1;
			box-shadow: 0 4px 51px 8px rgba(0, 0, 0, 0.06);
		}
		img {
			width: 100%;
		}
	}
}
.discount {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	overflow: hidden;
	height: 100%;
	min-height: 435px;
	padding-bottom: 20px;
	@media(max-width: 1023px) {
		min-height: 350px;
		padding-bottom: 15px;
	}
	@media(max-width: 450px) {
		min-height: 280px;
	}
	&__text {
		margin-top: 20px;
		padding: 0 22px;
		@media(max-width: 1023px) {
			margin-top: 15px;
			padding: 0 15px;
		}
		@media(max-width: 450px) {
			margin-top: 10px;
		}
	}
	&__title {
		color: #1f1f1f;
		font-size: 20px;
		font-weight: 500;
		@media(max-width: 1023px) {
			font-size: 16px;
		}
		@media(max-width: 450px) {
			font-size: 13px;
		}
	}
	&__discr {
		margin-top: 20px;
		color: #5c5b5b;
		font-size: 16px;
		font-weight: 400;
		@media(max-width: 1023px) {
			margin-top: 15px;
			font-size: 13px;
		}
		@media(max-width: 450px) {
			margin-top: 10px;
			font-size: 11px;
		}
	}
	&__block-btn {
		text-align: right;
	}
	&__btn {
		margin-top: 10px;
		margin-right: 22px;
		padding: 8px 13px;
		font-size: 16px;
		font-weight: 500;
		letter-spacing: 0.91px;
		// @media(max-width: 7670px) {
			
		// }
	}
}
.discounts {
	&__slider-nav {
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 auto;
	}
}
.discounts-arrow {
	position: relative;
	width: 21px;
	height: 2px;
	margin: 10px;
	font-size: 0;
	cursor: pointer;
	border: none;
	outline: none;
	background-color: #556d7b;
	@media(max-width: 450px) {
		width: 14px;
	}
	&:hover,
	&:hover::before,
	&:hover::after {
		background-color: #3d82d8;
	}
	&::before,
	&::after {
		position: absolute;
		content: '';
		width: 10px;
		height: 2px;
		outline: none;
		background-color: #556d7b;
		@media(max-width: 450px) {
			width: 7px;
		}
	}
}
.discounts-arrow__left {
	&::before {
		bottom: 3px;
		right: -1px;
		transform: rotate(45deg);
		@media(max-width: 450px) {
			bottom: 2px;
		}
	}
	&::after {
		transform: rotate(-45deg);
		top: 3px;
		right: -1px;
		@media(max-width: 450px) {
			top: 2px;
		}
	}
	
}
.discounts-arrow__right {
	&::before {
		bottom: 3px;
		left: -1px;
		transform: rotate(-45deg);
		@media(max-width: 450px) {
			bottom: 2px;
		}
	}
	&::after {
		transform: rotate(45deg);
		top: 3px;
		left: -1px;
		@media(max-width: 450px) {
			top: 2px;
		}
	}
}
.slider-count{
	font-size: 19px;
	font-weight: 500;
	color: #556d7b;
	letter-spacing: 0.27px;
	@media(max-width: 450px) {
		font-size: 11px;
	}
	&__line {
		margin: 0 2px;
	}
}

