.burger {
	position: relative;
	display: none;
	width: 30px;
	margin-top: 8px;
	padding: 0;
	font-size: 0;
	border:none;
	outline: none;
	cursor: pointer;
	background-color: transparent;
	@media (max-width: 1280px) {
		position: relative;
		display: block;
		width: 30px;
		height: 25px;
		//z-index: 12;
	}
	@media (max-width: 450px) {
		position: relative;
		display: block;
		width: 20px;
		height: 15px;
		//z-index: 12;
	}
