.lamination-bg {
	width: 100%;
	min-height: 1414px;
	padding-top: 208px;
	background: url('@{bg}lamination/laminat-bg.png') no-repeat top center;
	background-size: 1535px;
	@media(max-width:1380px) {
		min-height: 1200px;
		margin-top: 35px;
		padding-top: 0;
		background-size: 1200px;
	}
	@media(max-width:900px) {
		min-height: 780px;
		margin-top: 35px;
		padding-top: 0;
		background: url('@{bg}lamination/laminat-bg-mob.png') no-repeat center 65px;
	}
	@media(max-width:600px) {
		background: url('@{bg}lamination/bg-mob.png') no-repeat center 65px;
	}
}
.constructor {
	position: relative;
	display: flex;
	justify-content: space-between;
	width: 1539px;
	margin: 0 auto;
	margin-top: 101px;
	@media(max-width:1600px) {
		width: 1308px;
	}
	@media(max-width:1380px) {
		width: 1050px;
		margin-top: 155px;
	}
	@media(max-width:1100px) {
		flex-direction: column;
		align-items: center;
		width: 890px;
		height: 1025px;
		margin-top: 15px;
	}
	@media(max-width:900px) {
		width: 550px;
		height: 700px;
	}
	@media(max-width:600px) {
		width: 300px;
		height: 615px;

	}
	@media(max-width:450px) {
		margin-top: 32px;
	}
	&__left-dots {
		position: relative;
		width: 291px;
		height:710px;
		margin-left: 65px;
		@media(max-width:1600px) {
			width: 240px;
			margin-left: 0;
		}
		@media(max-width:1100px) {
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			width: 620px;
			height: 240px;
			order: 3;
		}
		@media(max-width:900px) {
			width: 340px;
			height: 154px;
		}
		@media(max-width:600px) {
			width: 260px;
			height: 140px;
		}
	}
	&__center-block {
		position: absolute;
		top: 100px;
		left: 50%;
		width: 256px;
		min-height: 643px;
		transform: translateX(-50%);
		@media(max-width:1100px) {
			top: 290px;
			width: 170px;
			min-height: 462px;
			order: 2;
		}
		@media(max-width:900px) {
			top: 235px;
			width: 90px;
			min-height: 247px;
			order: 2;
		}
		img {
			@media(max-width:1100px) {
				width: 100%;
				height: auto;
			}
		}
		
	}
	&__right-dots {
		position: relative;
		width: 356px;
		height:710px;
		@media(max-width:1600px) {
			width: 225px;
			margin-right: 16px;
		}
		@media(max-width:1380px) {
			width: 280px;
			margin-right: 15px;
		}
		@media(max-width:1100px) {
			position: absolute;
			top: 120px;
			left: 50%;
			transform: translateX(-50%);
			width: 700px;
			height: 110px;
			margin-right: 0;
			order: 1;
		}
		@media(max-width:900px) {
			width: 525px;
		}
		@media(max-width:600px) {
			top: 75px;
			width: 255px;
		}
	}
	&__window-handle {
		position: absolute;
		top: 114px;
		left: -17px;
		width: 98px;
		//height: 275px;
		z-index: 1;
		@media(max-width:1100px) {
			top: 80px;
			left: -15px;
			width: 80px;

		}
		@media(max-width:900px) {
			top: 40px;
			left: -15px;
			width: 47px;
			order: 2;
		}
	}
	&__window-laminate {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
	}
}
.lamination {
	&__slider-arrow {
		position: absolute;
		top: 53%;
		right: -73px;
		width: 120px;
		height: 360px;
		transform: translateY(-50%);
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-end;
		@media(max-width:1100px) {
			top: -15%;
			right: 160px;
			flex-direction: row;
			align-items: flex-end;
			width: 300px;
			height: 82px;
		}
		@media(max-width:900px) {
			right: 70px;
			align-items: flex-start;
			width: 200px;
			height: 40px;
		}
		&-left {
			@media(max-width:900px) {
				height: 50px;
			}
			@media(max-width:600px) {
				top: 100%;
				right: 34px;
			}
			.lamination__slider-arrow-content {
				@media(max-width:900px) {
					align-self: flex-end;
				}
			}
			
		}
		&-right {
			right: auto;
			left: -73px;
			align-items: flex-start;
			@media(max-width: 1380px) {
				left: -26px;
			}
			@media(max-width: 1100px) {
				top: 110%;
				left: 200px;
			}
			@media(max-width:900px) {
				top: 28%;
				left: 165px;
				align-items: flex-end;
				width: 200px;
				height: 50px;
			}
			@media(max-width:600px) {
				top: -12%;
				left: 30px;
				align-items: flex-end;
				width: 200px;
				height: 50px;
			}
			.lamination__slider-arrow-content {
				@media(max-width:900px) {
					align-self: flex-start;
				}
			}
			.lamination__slider-count-left {
				@media(max-width:900px) {
					order: 2;
				}
			}
			.lamination__slider-arrow-title {
				@media(max-width:900px) {
					order: 1;
				}
			}
		}
		&-content {
			text-align: center;
			@media(max-width:900px) {
				display: flex;
				flex-direction: column;
			}
			.slider-count {
				@media(max-width:900px) {
					font-size: 14px;
				}
			}
			
			
		}
		&-title {
			margin-bottom: 20px;
			color: #3e5564;
			font-size: 23px;
			font-weight: 500;
			line-height: 1.1;
			letter-spacing: 0.32px;
			@media(max-width:1100px) {
				margin-bottom: 5px;
				font-size: 16px;
			}
			@media(max-width:900px) {
				font-size: 12px;
			}
			@media(max-width:900px) {
				font-size: 11px;
			}
		}
	}
	&__arrow {
		width: 43px;
		height: 57px;
		padding: 0;
		border: none;
		background-color: transparent;
		cursor: pointer;
		outline: none;
		@media(max-width:900px) {
			width: 35px;
			height: 32px;
			background-size: 15px;
		}
		&:hover {
			opacity: 0.8;
		}
		img {
			width: 100%;
		}
	}
	
	&__arrow-left-next {
		background: url('@{bg}lamination/arrow-up-l.png') no-repeat center center/31px;
		@media(max-width:1100px) {
			transform: rotate(275deg);
		}
	}
	&__arrow-left-prew {
		background: url('@{bg}lamination/arrow-down-l.png') no-repeat center center/31px;
		@media(max-width:1100px) {
			transform: rotate(-96deg);
		}
	}
	&__arrow-right-next {
		background: url('@{bg}lamination/arrow-up-r.png') no-repeat center center/31px;
		@media(max-width:1100px) {
			transform: rotate(260deg);
		}
	}
	&__arrow-right-prew {
		background: url('@{bg}lamination/arrow-down-r.png') no-repeat center center/31px;
		@media(max-width:1100px) {
			transform: rotate(280deg);
		}
	}
}
.laminat-slider {
	width: 100%;
	height:710px;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	justify-content: space-between;
	@media(max-width:1380px) {
		background: none;
	}
	@media(max-width:1100px) {
		flex-direction: row;
		height: 110px;
	}
	&__left {
		padding: 70px 10px;
		padding-right: 30px;
		background: url('@{bg}lamination/left-line-bg.png') no-repeat top right;
		background-size: contain;
		@media(max-width:1600px) {
			padding-bottom: 30px;
		}
		@media(max-width:1100px) {
			flex-direction: row;
			align-items: flex-start;
			height: 249px;
			padding: 0;
		}
		@media(max-width:900px) {
			height: 175px;
		}
		@media(max-width:600px) {
			height: 133px;
		}
	}
	&__right {
		position: relative;
		align-items: flex-start;
		padding: 30px 10px;
		padding-left: 15px;
		background: url('@{bg}lamination/right-line-bg.png') no-repeat top left;
		background-size: contain;
		@media(max-width:1600px) {
			padding: 10px;
			padding-bottom: 9px;
		}
		@media(max-width:1380px) {
			padding-top: 0;
			padding-bottom: 16px;
		}
		@media(max-width:1100px) {
			padding: 0;
		}
	}
	&__item {
		display: none;
		align-items: center;
		cursor: pointer;
		@media(max-width:1600px) {
			flex-direction: column;
		}
		&:hover>.laminat-slider__img {
			box-shadow: 0 0 0 26px rgba(229, 231,249,.45);
			img {
				opacity: 0.8;
			}
		}
		img {
			width: 100%;
			height: auto;
		}
	}
	&__item-right {
		display: flex;
		align-items: center;
		cursor: pointer;
		@media(max-width:1600px) {
			flex-direction: column;
			align-items: flex-start;
		}
		@media(max-width:1380px) {
			align-items: center;
			text-align: center;
			//width: 155px;
		}
		@media(max-width:1100px) {
			position: absolute;
			width: 150px;
			align-items: center;
			text-align: center;
			margin: 0;
		}
		@media(max-width:900px) {
			width: 100px;
		}
		@media(max-width:600px) {
			width: 75px;
		}
		&:nth-child(2n) {
			margin-left: 61px;
			@media(max-width:1380px) {
				margin-left: 95px;
			}
		}
		&:nth-child(1) {
			@media(max-width:1100px) {
				position: absolute;
				top: 0;
				left: 0;
			}
			@media(max-width:600px) {
				top: auto;
				bottom: 0;
			}
		}
		&:nth-child(2) {
			@media(max-width: 1380px) {
				margin-left: 80px;
			}
			@media(max-width:1100px) {
				top: -62px;
				left: 31px;
			}
			@media(max-width:900px) {
				left: 0;
			}
			@media(max-width:600px) {
				top: 10px;
				left: 10px;
			}
		}
		&:nth-child(3) {
			@media(max-width:1100px) {
				top: -106px;
				left: 274px;
			}
			@media(max-width:900px) {
				left: 206px;
			}
			@media(max-width:600px) {
				top: auto;
				left: auto;
				bottom: 0;
				right: 0;
			}
		}
		&:nth-child(4) {
			@media(max-width: 1600px) {
				margin-left: 70px;
			}
			@media(max-width: 1380px) {
				margin-left: 110px;
			}
			@media(max-width:1100px) {
				top: -62px;
				right: 128px;
			}
			@media(max-width:900px) {
				right: 88px;
			}
			@media(max-width:600px) {
				display: none;
			}
		}
		&:nth-child(5) {
			@media(max-width: 1600px) {
				margin-left: 10px;
			}
			@media(max-width: 1380px) {
				margin-left: 20px;
			}
			@media(max-width:1100px) {
				top: 0;
				right: 0;
			}
			@media(max-width:600px) {
				display: none;
				// top: auto;
				// bottom: 0;
			}
		}
		
		&:hover>.laminat-slider__img {
			box-shadow: 0 0 0 19px rgba(229, 231,249,.45);
			img {
				opacity: 0.8;
			}
		}
		img {
			width: 100%;
			height: auto;
		}
		.laminat-slider__img {
			width: 78px;
			height: 78px;
			box-shadow: 0 0 0 19px rgba(229, 231,249,.25);
			order: 1;
			flex: none;
			@media(max-width:900px) {
				width: 42px;
				height: 41px;
				border: 2px solid #c5c5c5;
				box-shadow: 0 4px 51px 8px rgba(0, 0,0,.15);
			}
			@media(max-width:600px) {
				order: 2;
			}
		}
		.laminat-slider__title {
			margin-right: 0;
			margin-left: 20px;
			color: #5c5b5b;
			font-size: 20px;
			font-weight: 500;
			letter-spacing: 0.28px;
			order: 2;
			@media(max-width:1600px) {
				margin: 0;
				margin-top: 10px;
			}
			@media(max-width:1380px) {
				width: 140px;
			}
			@media(max-width:900px) {
				font-size: 14px;
			}
			@media(max-width:600px) {
				font-size: 11px;
				margin-top: 0;
				margin-bottom: 8px;
				order: 1;
			}
		}
	}
	&__item-current {
		width: 230px;
		justify-content: space-between;
		align-self: flex-start;
		@media(max-width:1600px) {
			width: auto;
			align-self: center;
		}
		@media(max-width:1100px) {
			align-self: flex-end;
		}
		@media(max-width:1100px) {
			align-self: center;
		}
		.laminat-slider__img {
			width: 139px;
			height: 139px;
			border: 3px solid #5199fd;
			box-shadow: 0 0 0 16px rgba(229, 231,249,.25);
			@media(max-width:900px) {
				width: 63px;
				height: 63px;
				box-shadow: 0 4px 51px 8px rgba(0, 0, 0, 0.15);
			}
		}
		.laminat-slider__title {
			color: #5199fc;
			font-size: 20px;
			font-weight: 700;
			letter-spacing: 0.28px;
			@media(max-width:900px) {
				margin-top: 10px;
				font-size: 14px;
			}
		}
	}
	&__item-current-right {
		align-self: flex-end;
		width: 265px;
		@media(max-width: 1600px) {
			width: 135px;
		}
		@media(max-width: 1380px) {
			margin-right: 10px;
		}
		.laminat-slider__img {
			width: 117px;
			height: 117px;
			flex: none;
			border: 3px solid #5199fd;
			@media(max-width:900px) {
				width: 62px;
				height: 61px;
				border: 3px solid #5199fd;
				box-shadow: 0 4px 51px 8px rgba(0, 0,0,.15);
			}
		}
		.laminat-slider__title {
			color: #5199fc;
			font-size: 20px;
			font-weight: 700;
			letter-spacing: 0.28px;
		}
	}
	&__title {
		margin-right: 20px;
		color: #5c5b5b;
		font-size: 20px;
		font-weight: 500;
		letter-spacing: 0.28px;
		@media(max-width:1600px) {
			margin: 0;
			margin-top: 20px;
			order: 2;
		}
		@media(max-width:900px) {
			margin-top: 10px;
			font-size: 14px;
		}
	}
	&__img {
		display: inline-block;
		font-size: 0;
		width: 108px;
		height: 108px;
		font-size: 0;
		border-radius: 50%;
		overflow: hidden;
		background-repeat: no-repeat !important;
		background-position: center center !important;
		background-size: cover !important;
		box-shadow: 0 0 0 26px rgba(229, 231,249,.25);
		@media(max-width:1600px) {
			order: 1;
		}
		@media(max-width:900px) {
			width: 56px;
			height: 56px;
			border: 3px solid #c5c5c5;
			box-shadow: 0 4px 51px 8px rgba(0, 0, 0, 0.15);
		}
		
	}
}
.laminat-slider__item-empty {
	cursor: default;
	.laminat-slider__img {
		box-shadow: none !important;
		border: none !important;
	}
	&:hover .laminat-slider__img {
		box-shadow: none !important;
	}
}
.show {
	display: flex;
}

.slide-up-title {
	position: relative;
	animation: lamination-down-title2 0.25s linear 1 reverse 0s forwards;
	@media(max-width: 1100px) {
		animation: lamination-down-title-mob2 0.25s linear 1 reverse 0s forwards;
	}
}
.slide-up-title2 {
	position: relative;
	animation: lamination-down-title 0.25s linear 1 reverse 0s forwards;
	@media(max-width: 1100px) {
		animation: lamination-down-title-mob 0.25s linear 1 reverse 0s forwards;
	}
}
.slide-up-img {
	position: relative;
	animation: lamination-down-img2 0.25s linear 1 reverse 0s forwards;
	@media(max-width: 1100px) {
		animation: lamination-down-img-mob2 0.25s linear 1 reverse 0s forwards;
	}
}
.slide-up-img2 {
	position: relative;
	animation: lamination-down-img 0.25s linear 1 reverse 0s forwards;
	@media(max-width: 1100px) {
		animation: lamination-down-img-mob 0.25s linear 1 reverse 0s forwards;
	}
}
.slide-down-title {
	position: relative;
	animation: lamination-down-title 0.25s linear 1 normal 0s forwards;
	@media(max-width: 1100px) {
		animation: lamination-down-title-mob2 0.25s linear 1 normal 0s forwards;
	}
}
.slide-down-title2 {
	position: relative;
	animation: lamination-down-title2 0.25s linear 1 normal 0s forwards;
	@media(max-width: 1100px) {
		animation: lamination-down-title-mob2 0.25s linear 1 normal 0s forwards;
	}
}
.slide-down-img {
	position: relative;
	animation: lamination-down-img 0.25s linear 1 normal 0s forwards;
	@media(max-width: 1100px) {
		animation: lamination-down-img-mob 0.25s linear 1 normal 0s forwards;
	}
}
.slide-down-img2 {
	position: relative;
	animation: lamination-down-img2 0.25s linear 1 normal 0s forwards;
	@media(max-width: 1100px) {
		animation: lamination-down-img-mob2 0.25s linear 1 normal 0s forwards;
	}
	
}
@keyframes lamination-down-img {
	 from {
		top: 0;
		right: 0;
		opacity: 1;
		transform: scale(1);
	}
	to {
		top: 80px;
		right: 20px;
		opacity: 0.3;
		transform: scale(0.4);
	}
}
@keyframes lamination-down-img2 {
	 from {
		top: -80px;
		right: -20px;
		opacity: 0.3;
		transform: scale(0.4);
	}
	to {
		top: 0;
		right: 0;
		opacity: 1;
		transform: scale(1);
	}
}
@keyframes lamination-down-img-mob {
	 from {
		left: 0;
		opacity: 1;
		transform: scale(1);
	}
	to {
		left: 80px;
		opacity: 0.3;
		transform: scale(0.2);
	}
}
@keyframes lamination-down-img-mob2 {
	 from {
		left: -80px;
		opacity: 0.3;
		transform: scale(0.2);
	}
	to {
		left: 0;
		opacity: 1;
		transform: scale(1);
	}
}
@keyframes lamination-down-title {
	 from {
		top: 0;
		opacity: 0.9;
	}
	to {
		top: 80px;
		opacity: 0;
	}
}
@keyframes lamination-down-title2 {
	 from {
		top: -80px;
		opacity: 0;
	}
	to {
		
		top: 0;
		opacity: 1;
	}
}
@keyframes lamination-down-title-mob {
	 from {
		left: 0;
		opacity: 0.9;
	}
	to {
		left: 80px;
		opacity: 0;
	}
}
@keyframes lamination-down-title-mob2 {
	 from {
		left: -80px;
		opacity: 0;
	}
	to {
		left: 0;
		opacity: 1;
	}
}
.handle-anim {
	animation: handle-anim 0.5s linear 1 normal 0s forwards;
}
@keyframes handle-anim {
	 from {
		opacity: 0.5;
	}
	to {
		opacity: 1;
	}
}