.nav-list {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -15px;
	@media (max-width: 1280px) {
		flex-direction: column;
		align-items: center;
		width: 100%;
		margin: 0;
		padding-top: 100px;
	}
	&__item {
		margin: 0 15px;
		@media(max-width:1480px) {
			margin: 0 10px;
		}
		@media(max-width:1280px) {
			margin: 5px 0;
		}
		// @media (max-width: 767px) {
		// 	text-align: center;
		// 	margin-left: 0;
		// 	margin-top: 15px;
		// }
	}
	&__link {
		position: relative;
		padding-top: 8px;
		font-family: "Proxima Nova", sans-serif;
		color: #ffffff;
		font-size: 20px;
		font-weight: 400;
		letter-spacing: 0.6px;
		text-transform: none;
		transition: 0.3s ease;
		@media (max-width: 767px) {
			padding-top: 0;
		}
		&:hover,
		&:focus {
			//color:@c-orange;
		}
	}
