.calc {
	margin-top: 20px;
	&__gen-title {
		margin-top: 25px;
		@media (max-width: 450px) {
			margin-top: 0;
		}
		span {
			@media (max-width: 600px) {
				display: block;
				padding-left: 0;
			}
		}
		
	}
	&__title {
		margin-top: 25px;
		color: #1f1f1f;
		font-size: 20px;
		font-weight: 500;
		@media (max-width: 1023px) {
			margin-top: 20px;
		}
		@media (max-width: 600px) {
			margin-top: 15px;
			font-size: 12px;
		}
		span {
			color: #989898;
			font-size: 18px;
			
			@media (max-width: 600px) {
				font-size: 11px;
			}
		}
	}
	&__discr {
		max-width: 760px;
		margin: 0 auto;
		margin-top: 70px;
		padding: 0 20px;
	}
	&__wrap {
		width: 100%;
		max-width: 1450px;
		margin: 0 auto;
		margin-top: 50px;
		padding: 0 20px;
		padding-bottom: 100px;
		@media (max-width: 1280px) {
			max-width: 910px;
		}
		@media (max-width: 450px) {
			padding-bottom: 20px;
		}
	}
	&-form__inner {
		display: flex;
		justify-content: space-between;
			@media (max-width: 1280px) {
				flex-wrap: wrap;
				justify-content: start;
			}
	}
	&__left-block {
		margin-right: 100px;
		@media (max-width: 1380px) {
			margin-right: 50px;
		}
		@media (max-width: 1280px) {
			width: 100%;
			margin-right: 0;
		}
	}
	&__right-block {
		width: 100%;
		@media (max-width: 450px) {
			position: absolute;
			top: 100px;
			left: 0;
			padding: 35px 25px 20px;
			background-color: #e2eaf5;
			z-index: 10;
			display: none;
		}
	}
	&__img {
		position: relative;
		padding-right: 40px;
		@media (max-width: 1280px) {
			width: 450px;
			height: 310px;
			margin: 0 auto;
		}
		@media (max-width: 900px) {
			width: 281px;
			height: 215px;
			margin: 0 auto;
		}
		img {
			@media (max-width: 1280px) {
				width: 100%;
				height: auto;
			}	
		}
	}
	&__window-options {
		position: relative;
		display: flex;
		justify-content: center;
		width: 100%;
		margin-left: -10px;
		margin-top: 50px;
		@media (max-width: 1023px) {
			margin-top: 25px;
		}
		@media (max-width: 600px) {
			flex-wrap: wrap;
			justify-content: start;
			margin-top: 10px;
		}
		&-select {
			bottom: -85px;
			left: 95px;
			max-width: 680px;
			@media (max-width: 1023px) {
				bottom: -97px;
				justify-content: start;
				flex-wrap: wrap;
				max-width: 290px;
			}
			@media (max-width: 600px) {
				bottom: -90px;
				justify-content: start;
				flex-wrap: wrap;
				max-width: 290px;
			}
			@media (max-width: 460px) {
				bottom: -50px;
				left: 20px;
			}
		}
		&-item {
			width: 230px;
			margin-left: 9px;
			@media (max-width: 600px) {
				width: 131px;
				margin-top: 10px;
			}
		}
	}
	&__window-select {
		position: relative;
		padding: 10px 15px;
		padding-right: 45px;
		color: #9897ac;
		font-size: 17px;
		font-weight: 400;
		letter-spacing: 0.27px;
		cursor: pointer;
		border-radius: 4px;
		border: 1px solid #8d97a8;
		@media (max-width: 1023px) {
			padding: 7px 15px;
			padding-right: 32px;
			font-size: 14px;
		}
		@media (max-width: 600px) {
			padding: 5px 5px;
			padding-right: 24px;
			font-size: 10px;
		}	
		span {
			display: block;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: clip;
		}
		&::before {
			position: absolute;
			content: '';
			display: none;
			right: 9px;
			bottom: -26px;
			border-width: 15px 10px 15px 10px;
			border-style: solid;
			border-color: transparent transparent #3d82d8 transparent;
			z-index: 1;
			@media (max-width: 1023px) {
				right: 5px;
			}
			@media (max-width: 600px) {
				right: 7px;
				bottom: -18px;
				border-width: 12px 5px 12px 5px;
			}
			@media (max-width: 460px) {
				right: 6px;
    		bottom: -15px;
			}
		}
		&--active::before {
			display: block;
		}
	}
	&__window-sizes {
		display: flex;
		margin-top: 10px;
		justify-content: space-between;
		max-width: 470px;
		@media (max-width: 1023px) {
			margin-top: 0;
		}
		@media (max-width: 450px) {
			justify-content: start;
		}
	}
	&__window-width,
	&__window-height {
		input {
			width: 192px;
			margin-top: 25px;
			padding: 11px 15px;
			color: #9897ac;
			font-size: 20px;
			font-weight: 400;
			letter-spacing: 0.28px;
			background-color: transparent;
			border-radius: 4px;
			border: 1px solid #99a3b2;
			@media (max-width: 1023px) {
				margin-top: 20px;
				padding: 8px 15px;
			}
			@media (max-width: 600px) {
				margin-top: 15px;
				padding: 8px;
				font-size: 12px;
			}
			@media (max-width: 450px) {
				width: 111px;
			}
		}
		input::-webkit-outer-spin-button,
		input::-webkit-inner-spin-button {
			-webkit-appearance: none;
			margin: 0; 
		}
	}
	&__window-height {
		margin-left: 15px;
	}
	&__window-glass {
		margin-top: 40px;
		@media (max-width: 1023px) {
			margin-top: 25px;
		}
		@media (max-width: 600px) {
			margin-top: 20px;
		}
		&-item {
			margin-top: 25px;
			@media (max-width: 600px) {
				margin-top: 15px;
			}
		}
		&-select {
			top: 65px;
			right: -25px;
			display: none;
			max-width: 460px;
		}
	}
	&__window-color {
		margin-top: 40px;
		@media (max-width: 1023px) {
			margin-top: 25px;
		}
		@media (max-width: 600px) {
			margin-top: 20px;
		}
	}
	&__btn-block {
		margin-top: 50px;
		@media (max-width: 1023px) {
			margin-top: 25px;
		}
		@media (max-width: 600px) {
			margin-top: 20px;
		}
		@media (max-width: 450px) {
			justify-content: center;
		}
		&-desk {
			@media (max-width: 450px) {
				display: none;
			}
		}
		&-mob {
			display: none;
			@media (max-width: 450px) {
				display: flex;
			}
		}
	}
	&__btn {
		@media(max-width: 450px) {
			min-width: auto;
			padding: 7px 18px;
		}
	}
	&-form__orders {
		margin-top: 65px;
		@media (max-width: 600px) {
			margin-top: 25px;
		}
	}
	&-form__orders-item {
		margin-top: 25px;
		@media (max-width: 600px) {
			margin-top: 10px;
		}
	}
	&-form__submit {
		text-align: right;
		@media (max-width: 450px) {
			text-align: center;
		}
	}
}
.order {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 55px 50px;
	border-radius: 13px;
	background-color: #ffffff;
	@media (max-width: 1280px) {
		flex-direction: column;
		align-items: flex-start;
		padding: 30px 50px;
		padding-bottom: 15px;
	}
	@media (max-width: 600px) {
		padding: 15px;
		padding-top: 20px;
	}
	@media (max-width: 450px) {
		padding: 10px;
		padding-top: 15px;
	}
	&--bg {
		background-color: #ebf2fb;
	}
	&__left-block {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex: 0 1 365px;
		@media (max-width: 1280px) {
			flex: none;
			justify-content: flex-start;
		}
	}
	&__count {
		color: #1f1f1f;
		font-size: 20px;
		font-weight: 500;
		@media (max-width: 600px) {
			font-size: 14px;
		}
		@media (max-width: 450px) {
			font-size: 11px;
		}
	}
	&__img {
		@media (max-width: 1280px) {
			margin-left: 30px;
		}
		@media (max-width: 600px) {
			width: 25px;
			height: 17px;
		}
		@media (max-width: 450px) {
			margin-left: 20px;
		}
		img {
			width: 100%;
			height: auto;
		}
	}
	&__left-block-text {
		@media (max-width: 1280px) {
			margin-left: 30px;
		}
		@media (max-width: 450px) {
			margin-left: 20px;
		}
	}
	&__title {
		display: block;
		color: #1f1f1f;
		font-size: 20px;
		font-weight: 500;
		@media (max-width: 600px) {
			font-size: 14px;
		}
		@media (max-width: 450px) {
			font-size: 12px;
		}
	}
	&__win-size {
		display: block;
		margin-top: 16px;
		color: #5c5b5c;
		font-size: 19px;
		font-weight: 400;
		letter-spacing: 0.27px;
		@media (max-width: 1280px) {
			margin-top: 8px;
		}
		@media (max-width: 600px) {
			margin-top: 5px;
			font-size: 13px;
		}
		@media (max-width: 450px) {
			margin-top: 3px;
			font-size: 11px;
		}
		span {
			font-style: italic;
		}
	}
	&__discr {
		flex: 0 1 600px;
		@media (max-width: 1280px) {
			flex: none;
			margin-top: 20px;
		}
		@media (max-width: 600px) {
			margin-top: 15px;
		}
		&--reg	{
			color: #5c5b5c;
			font-size: 19px;
			font-weight: 400;
			letter-spacing: 0.27px;
			@media (max-width: 600px) {
				font-size: 13px;
			}
			@media (max-width: 450px) {
				font-size: 11px;
			}
		}
		&--italic {
			margin-top: 17px;
			color: #5c5b5c;
			font-size: 17px;
			letter-spacing: 0.24px;
			font-weight: 400;
			font-style: italic;
			@media (max-width: 1280px) {
				margin-top: 10px;
			}
			@media (max-width: 600px) {
				margin-top: 5px;
				font-size: 12px;
			}
			@media (max-width: 450px) {
				font-size: 10px;
			}
		}
	}
	&__block-btn {
		display: flex;
		justify-content: space-between;
		flex: 0 1 110px;
		@media (max-width: 1280px) {
			justify-content: flex-end;
			flex: none;
			width: 100%;
			margin-top: 15px;
		}
		@media (max-width: 450px) {
			margin-top: 5px;
		}
	}
	&__btn {
		width: 32px;
		height: 35px;
		font-size: 0;
		cursor: pointer;
		border: none;
		background-color: transparent;
		@media (max-width: 1280px) {
			margin-left: 20px;
		}
		@media (max-width: 600px) {
			width: 18px;
			height: 20px;
		}
		&-copy {
			background: url('@{bg}calc/copy-icon.png') no-repeat top center/contain;
			&:hover {
				background: url('@{bg}calc/copy-icon-hov.png') no-repeat top center/contain;
			}
		}
		&-del {
			background: url('@{bg}calc/delete-icon.png') no-repeat top center/contain;
			&:hover {
				background: url('@{bg}calc/delete-icon-hov.png') no-repeat top center/contain;
			}
		}
	}
}



.arrow-btn {
	position: absolute;
	width: 42px;
	height: 42px;
	right: -1px;
	top: -1px;
	border-top-right-radius: 4px;
	border-bottom-right-radius: 4px;
	background-color: #3d82d8;
	@media (max-width: 1023px) {
		width: 32px;
		height: 32px;
	}
	@media (max-width: 600px) {
		width: 24px;
		height: 24px;
	}
	&:hover {
		background-color: #5c96de;
	}
	&::before {
		position: absolute;
		content: '';
		right: 11px;
		top: 16px;
		border-width: 12px 9px 12px 9px;
		border-style: solid;
		border-color: #ffffff transparent transparent transparent;
		z-index: 1;
		@media (max-width: 1023px) {
			top: 13px;
			border-width: 10px 5px 10px 5px;
		}
		@media (max-width: 600px) {
			top: 10px;
			right: 8px;
			border-width: 8px 4px 8px 4px;
		}
	}
	&--active {
		&::before {
			top: 2px;
			border-color: transparent transparent #ffffff transparent;
			@media (max-width: 1023px) {
				top: 1px;
			}
			@media (max-width: 600px) {
				top: 1px;
			}
		}
	}
}
.ruler {
	position: absolute;
	width: 100%;
	color: #989898;
	font-size: 17px;
	font-weight: 300;
	text-align: center;
	@media (max-width: 900px) {
		width: 74%;
		font-size: 11px;
	}
	&::before,
	&::after {
		position: absolute;
		content: '';
		width: 40%;
		height: 11px;
		border-bottom: 1px solid #898989;
		@media (max-width: 1280px) {
			width: 35%;
		}
		@media (max-width: 900px) {
			width: 30%;
			height: 8px;
		}
	}
	&::before {
		left: 0;
		top: 0;
		border-left: 1px solid #898989;
	}
	&::after {
		right: 0;
		top: 0;
		border-right: 1px solid #898989;
	}
	&__right {
		bottom: 50%;
		right: -26%;
		max-width: 415px;
		transform: rotate(-90deg);
		@media (max-width: 1280px) {
			max-width: 245px;
		}
		@media (max-width: 900px) {
			bottom: 54%;
			right: -26%;
			max-width: 150px;
		}
	}
	&__bot {
		bottom: 0;
		left: 52%;
		max-width: 580px;
		transform: translateX(-50%);
		@media (max-width: 1280px) {
			bottom: -7px;
			left: 50%;
			max-width: 345px;
		}
		@media (max-width: 900px) {
			bottom: 5px;
			left: 47%;
		}
	}
}
.select-list {
	position: absolute;
	width: 100%;
	display: flex;
	display: none;
	justify-content: space-between;
	align-items: center;
	padding: 15px 35px;
	cursor: pointer;
	box-shadow: 0 4px 51px 8px rgba(0, 0, 0, 0.14);
	border-radius: 10px;
	background-color: #3d82d8;
	z-index: 2;
	@media (max-width: 1023px) {
		padding: 8px 27px;
	}
	&--active {
		display: flex;
	}
	&__column {
		flex-direction: column;
	}
	&__item {
		margin: 5px;
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		letter-spacing: 0.29px;
		@media (max-width: 1023px) {
			font-size: 16px;
		}
		&:hover {
			opacity: 0.8;
		}
		&-active {
			color: #bbbbbd;
		}
	}
}
.window-type {
	&__list {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 20px;
		padding: 14px 0;
		@media (max-width: 600px) {
			margin-top: 15px;
			padding: 5px 0;
		}
		@media (max-width: 450px) {
			justify-content: start;
			flex-wrap: wrap;
			margin-top: 0;
		}
	}
	&__list-item {
		margin-right: 20px;
		opacity: 0.3;
		cursor:pointer;
		@media (max-width: 450px) {
			margin-top: 20px;
		}
		&:hover {
			opacity: 0.7;
		}
		&-active {
			opacity: 1;
			&:hover {
				opacity: 1;
			}
		}

	}
}
.choose-color {
	display: flex;
	align-items: center;
	margin-top: 25px;
	@media (max-width: 600px) {
		margin-top: 15px;
	}
	&__item {
		width: 44px;
		height: 44px;
		border-radius: 14px;
		border: 3px solid transparent;
		overflow: hidden;
		@media (max-width: 600px) {
			width: 25px;
			height: 25px;
		}
		&--active {
			border: 3px solid #2ca4eb;
		}
	}
	&__current {
		background-color: #e5e5e5;
		@media (max-width: 600px) {
			border-radius: 50%;
		}
	}
	&__menu {
		margin-left: 25px;
		cursor: pointer;
		background-image: url('@{bg}calc/colored-icon.jpg');
		background-repeat: no-repeat;
		background-size: cover;
	}
	&__palette {
		display: none;
	}
}
.calc-bg {
	position: relative;
	padding: 80px 0;
	background-color: #f0f5fc;
	@media(max-width: 600px) {
		padding: 60px 0;
		}
	@media(max-width: 450px) {
		padding: 40px 0;
	}
	&::after,
	&::before {
		content:'';
		position: absolute;
		height: 85px;
		background: #fff;
		width: 100%;
		display: block;
		@media(max-width: 600px) {
			height: 60px;
		}
		@media(max-width: 450px) {
			height: 40px;
		}
	}
	&::before {
		top: 0;
		left: 0;
		background: linear-gradient(to left top, transparent 50%, #ffffff 50%);
	}
	&::after {
		bottom: 0;
		right: 0;
		background: linear-gradient(to left bottom, transparent 50%, #ffffff 50%);
	}
}
.close {
	position: absolute;
	top: 20px;
	right: 20px;
	width: 23px;
	height: 23px;
	font-size: 0;
	border-radius: 50%;
	border: 2px solid #a7a7a7;
	display: none;
	@media (max-width:450px) {
		display: block;
	}
	&::after,
	&::before {
		content:'';
		position: absolute;
		width: 12px;
		height: 2px;
		background: #a7a7a7;
		top: 8px;
		left: 4px;
	}
	&::after {
		transform: rotate(-45deg);
	}
	&::before {
		transform: rotate(45deg);
	}
}