.portfolio-bg {
	background: url('@{bg}portfolio/portfolio-bg.jpg') no-repeat center center/cover;
}
.portfolio {
	&__title {
		position: relative;
		padding-bottom: 35px;
		color: #ffffff;
		span {
			color: #ffffff;
		}
		&::after {
			position: absolute;
			content: '';
			width: 80px;
			height: 2px;
			bottom: 5px;
			left: 50%;
			transform: translateX(-50%);
			background-color: #ffffff;
		}
	}
	&__container {
		width: 100%;
		max-width: 1445px;
		margin: 0 auto;
		padding: 0 20px;
		padding-bottom: 30px;
		img {
			width: 100%;
			height: auto;
		}
	}
	&__slider {
		
	}
	&__slider-item {
		display: flex;
		justify-content: space-between;
		
	}
	&__left-block {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		max-width: 808px;
		margin-bottom: -25px;
		&-item {
			width: 100%;
			max-width: 383px;
			margin-bottom: 25px;
			margin-right: 20px;
		}
	}
	&__row {
		display: flex;
		width: 100%;
	}
	&__right-block {
		display: flex;
		width: 100%;
		max-width: 585px;
	}
}
.card {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	border-radius: 13px;
	overflow: hidden;
	background-color: #ffffff;
	&__img {
		font-size: 0;
	}
	&__content {
		padding: 35px 30px 45px;
		&-big {
			padding: 35px 35px 30px;
		}
	}
	&__title {
		display: block;
		color: #7c7b91;
		font-size: 25px;
		font-weight: 500;
		letter-spacing: 0.36px;
	}
	&__discr {
		word-wrap: break-word;
		color: #9999a5;
		font-size: 17px;
		font-weight: 400;
		letter-spacing: 0.24px;
		&-big {
			margin-top: 25px;
		}
	}
}